/* SCSS Document */
@charset "UTF-8";

/****************************************************************************
  해외여론주도층 분석 한정 스타일
****************************************************************************/
#container.leaders {
  /*
  ■■■■■■■■■■■■■■■■■■■■■■■■■■■■■■■■■■■■■■■■■■■■■■■■■■■■■■■■■■■■■■■■■■■■■■■■■■■■■■■■■■■■■■■■■■■■■■■■■■
  */

  /* 주요현황 */
  .l-section[data-section="주요현황"] {
    $section: "l-section[data-section='주요현황']";

    .form-topic-list {
      $fieldset: form-fieldset;
      $check: form-check;

      display: flex;
      justify-content: space-between;
      margin: 0 -0.5rem;
      .form-check {
        width: 100%;
        padding: 0 0.5rem;
        opacity: 0;
        transform: translateY(20%);
        &__label {
          flex-flow: wrap column;
          height: 12rem;
          width: 100%;
          border-width: 0.3rem;
          border-radius: 10em;
          .tit {
            font-size: 1.5rem;
            font-weight: 700;
          }
          .count {
            font-size: 2.4rem;
            font-weight: 700;
          }
          .percent {
            font-size: 1.3rem;
            font-weight: 700;
            &::before {
              content: "(";
            }
            &:after {
              content: "%)";
            }
          }
          &:hover {
            z-index: 1;
            transform: translateY(-5%);
          }
        }
      }

      @at-root .#{$fieldset}.form-fieldset--is-active .#{$check} {
        @for $i from 1 through 10 {
          &:nth-child(#{$i}) {
            opacity: 1 !important;
            transform: translateY(0) !important;
            @include transition($time: 1, $delay: #{$i * 0.05 + 0.5});
          }
        }
      }
    }
  }

  /*
  ■■■■■■■■■■■■■■■■■■■■■■■■■■■■■■■■■■■■■■■■■■■■■■■■■■■■■■■■■■■■■■■■■■■■■■■■■■■■■■■■■■■■■■■■■■■■■■■■■■
  */
}
