/* SCSS Document */
@charset "UTF-8";

/****************************************************************************
    forms - 폼 요소
****************************************************************************/
/* size */
$form-font-size: 1.2rem;

@mixin formSizes() {
  &--sm {
    font-size: $form-font-size - 0.2;
  }
  &--lg {
    font-size: $form-font-size + 0.4;
  }
  &--xlg {
    font-size: $form-font-size + 0.6;
  }
}

/*
■■■■■■■■■■■■■■■■■■■■■■■■■■■■■■■■■■■■■■■■■■■■■■■■■■■■■■■■■■■■■■■■■■■■■■■■■■■■■■■■■■■■■■■■■■■■■■■■■■
*/

/* size & color  */
$formStates: (
  --default-border-color: $G-default-border-color,
  --default-color: $G-default-color,
  --default-background-color: $G-default-background-color,
  --focus-border-color: $G-focus-border-color,
  --focus-background-color: $G-focus-background-color,
  --checked-border-color: $G-checked-border-color,
  --checked-color: $G-checked-color,
  --checked-background: $G-checked-background-color,
  --checked-background-color: $G-checked-background-color,
  --disabled-border-color: $G-disable-border-color,
  --disabled-color: $G-disable-color,
  --disabled-background: $G-disable-background-color,
  --disabled-background-color: $G-disable-background-color,
);

@mixin formStates($val: $formStates) {
  input {
    border-color: map-get($val, --default-border-color);
    background: map-get($val, --default-background);
    @include formSizes;
  }
  label {
    color: map-get($val, --default-color);
  }
  @at-root .form-row:not([data-text-value=""]) input {
    background-color: map-get($val, --focus-background-color);
  }
  input:focus {
    background-color: map-get($val, --focus-background-color);
  }
  input:checked {
    border-color: map-get($val, --checked-border-color);
    background-color: map-get($val, --checked-background-color);
  }
  input[disabled] {
    border-color: map-get($val, --disabled-border-color);
    background-color: map-get($val, --disabled-background-color);
  }
}

/*
■■■■■■■■■■■■■■■■■■■■■■■■■■■■■■■■■■■■■■■■■■■■■■■■■■■■■■■■■■■■■■■■■■■■■■■■■■■■■■■■■■■■■■■■■■■■■■■■■■
*/

/* layout */
.form {
  position: relative;
}

/* .form-fieldset */
.form-fieldset {
  $f: &;
  position: relative;
  .form-check-button--group {
    #{$f} & {
      border: 10px solid red;
    }
  }
  -webkit-user-select: none;
  -moz-user-select: none;
  -ms-user-select: none;
  user-select: none;
}

/* .form-row[data-row] */
.form-row {
  position: relative;

  @include formStates;
}

/*
■■■■■■■■■■■■■■■■■■■■■■■■■■■■■■■■■■■■■■■■■■■■■■■■■■■■■■■■■■■■■■■■■■■■■■■■■■■■■■■■■■■■■■■■■■■■■■■■■■
*/

/* input 기본값 */
.form-text {
  font-size: $form-font-size;
  &__textarea,
  &__input {
    display: block;
    width: 100%;
    height: 100%;
    padding: fz-in-em(13, $form-font-size);
    border-width: 1px;
    border-style: solid;
    border-radius: fz-in-em(8, $form-font-size);
    background-color: #fff;
    font-weight: 400;
    line-height: 1.5;
    @include transition($tgs: "color, border , background");

    &:not([readonly]) {
      &:focus {
        border-color: $G-focus-border-color;
        outline: 0;
      }
    }
  }
}

/*
■■■■■■■■■■■■■■■■■■■■■■■■■■■■■■■■■■■■■■■■■■■■■■■■■■■■■■■■■■■■■■■■■■■■■■■■■■■■■■■■■■■■■■■■■■■■■■■■■■
*/

/* search */
.form-search {
  $f: &;

  font-size: $form-font-size;
  &__input {
    display: block;
    width: 100%;
    min-width: 18rem;
    height: fz-in-em(24, $form-font-size);
    padding: 0 fz-in-em(45, $form-font-size) 0 fz-in-em(13, $form-font-size);
    border: 1px solid #ededef;
    border-radius: 2em;
    background-color: #fff;
    font-weight: 400;
    line-height: 1.5;
    outline: 0;
    @include transition($tgs: "color, border , background");
  }
  &__btn-clear {
    display: none;
    position: absolute;
    top: 50%;
    z-index: 1;
    right: fz-in-em(25, $form-font-size) !important;
    width: fz-in-em(10, $form-font-size);
    height: fz-in-em(10, $form-font-size);
    background-image: svgIcon(close, 999);
    background-position: center center;
    background-repeat: no-repeat;
    background-size: contain;
    transform: translateY(-50%);
  }
  &__btn-search {
    position: absolute;
    right: 0;
    top: 50%;
    z-index: 1;
    right: fz-in-em(4, $form-font-size);
    width: fz-in-em(17, $form-font-size);
    height: fz-in-em(17, $form-font-size);
    border-radius: 100%;
    background-image: svgIcon(search, fff);
    background-position: center center;
    background-repeat: no-repeat;
    background-size: 50%;
    background-color: map-get($formStates, --checked-background-color);
    transform: translateY(-50%);
  }
  &[data-text-value] {
    #{$f}__btn-clear {
      display: block;
    }
    &[data-text-value=""] {
      #{$f}__btn-clear {
        display: none;
      }
      #{$f}__btn-search {
        background-color: #a9aaac;
      }
    }
  }

  &--h-lg {
    #{$f}__input {
      height: 2.8rem;
      padding-right: fz-in-em(55, $form-font-size);
    }
    #{$f}__btn-clear {
      right: fz-in-em(35, $form-font-size) !important;
    }
    #{$f}__btn-search {
      right: fz-in-em(4, $form-font-size) !important;
      width: fz-in-em(20, $form-font-size);
      height: fz-in-em(20, $form-font-size);
    }
  }

  @include formSizes;
}

/*
■■■■■■■■■■■■■■■■■■■■■■■■■■■■■■■■■■■■■■■■■■■■■■■■■■■■■■■■■■■■■■■■■■■■■■■■■■■■■■■■■■■■■■■■■■■■■■■■■■
*/

/* select */
.form-select {
  font-size: $form-font-size;
  &__select {
    display: block;
    position: relative;
    width: 100%;
    min-width: fz-in-em(140, $form-font-size);
    height: fz-in-em(24, $form-font-size);
    padding: 0 fz-in-em(30, $form-font-size) 0 fz-in-em(10, $form-font-size);
    border-bottom: 1px solid map-get($formStates, --disabled-border-color);
    font-weight: 400;
    color: map-get($formStates, --default-color);
    line-height: 1.5;
    background-image: svgIcon(triangle-down, 999);
    background-position: right fz-in-em(10, $form-font-size) center;
    background-repeat: no-repeat;
    background-size: fz-in-em(8, $form-font-size);
  }
}

/*
■■■■■■■■■■■■■■■■■■■■■■■■■■■■■■■■■■■■■■■■■■■■■■■■■■■■■■■■■■■■■■■■■■■■■■■■■■■■■■■■■■■■■■■■■■■■■■■■■■
*/

/* checkbox */
.form-check {
  $f: &;

  display: inline-flex;
  font-size: $form-font-size;
  align-items: center;
  &__input {
    display: inline-block;
    width: fz-in-em(24, $form-font-size);
    height: fz-in-em(24, $form-font-size);
    border-width: 1px;
    border-style: solid;
    border-radius: fz-in-em(4, $form-font-size);
    cursor: pointer;
    @include transition;

    ~ label {
      margin-left: 0.5rem;
    }
  }
  &__input:checked {
    border: 0;
    background-image: svgIcon(check, fff);
    background-position: center center;
    background-repeat: no-repeat;
    background-size: fz-in-em(13, $form-font-size);
  }
  &__label {
    position: relative;
    display: inline-block;
    font-size: $form-font-size;
  }
  &__input[type="radio"] {
    border-radius: 100%;
  }
  &--h-lg {
    #{$f}__input {
      height: fz-in-em(28, $form-font-size);
    }
  }
}

/*
■■■■■■■■■■■■■■■■■■■■■■■■■■■■■■■■■■■■■■■■■■■■■■■■■■■■■■■■■■■■■■■■■■■■■■■■■■■■■■■■■■■■■■■■■■■■■■■■■■
*/

/* checkbox - switches */
.form-switch {
  .form-check__input {
    width: fz-in-em(40, $form-font-size);
    height: fz-in-em(24, $form-font-size);
    border-width: 0;
    border-radius: 2em;
    background-image: svgIcon(close, A9AAAC), svgIcon(circle, fff);
    background-position: 24% center, left center;
    background-size: fz-in-em(9, $form-font-size), fz-in-em(25, $form-font-size);
    background-color: map-get($formStates, --disabled-background-color);
    @include transition($tgs: background-position);
  }
  .form-check__input:checked {
    border: 0;
    background-image: svgIcon(check, 000), svgIcon(circle, fff);
    background-position: 76% center, right center;
  }
}

/*
■■■■■■■■■■■■■■■■■■■■■■■■■■■■■■■■■■■■■■■■■■■■■■■■■■■■■■■■■■■■■■■■■■■■■■■■■■■■■■■■■■■■■■■■■■■■■■■■■■
*/

/* checkbox - button */
.form-check-button {
  $f: &;
  $coloSet: --color-set;
  .form-check__input {
    @include visuallyHidden;
  }
  .form-check__label {
    overflow: hidden;
    display: inline-flex;
    position: relative;
    justify-content: center;
    align-items: center;
    position: relative;
    height: fz-in-em(24, $form-font-size);
    padding: 0 fz-in-em(15, $form-font-size);
    margin: 0;
    color: var($coloSet, var(--default-font-color));
    word-break: keep-all;
    border-width: 1px;
    border-style: solid;
    border-color: var($coloSet, var(--default-border-color, map-get($formStates, --default-border-color)));
    border-radius: 2em;
    background-color: var(--default-background-color, map-get($formStates, --default-background-color));
    @include transition;
  }
  .form-check__input:checked + .form-check__label {
    color: map-get($formStates, --checked-color);
    background-color: var($coloSet, var(--checked-background-color, map-get($formStates, --checked-background-color)));
    border-color: transparent;
  }
  &--h-lg {
    .form-check__label {
      height: fz-in-em(28, $form-font-size);
    }
  }
}

/*
■■■■■■■■■■■■■■■■■■■■■■■■■■■■■■■■■■■■■■■■■■■■■■■■■■■■■■■■■■■■■■■■■■■■■■■■■■■■■■■■■■■■■■■■■■■■■■■■■■
*/

/* checkbox - button - switch */
.form-check-button--switch {
  .form-check__label {
    border-radius: 0;
    z-index: 1;
  }
  &:first-of-type {
    .form-check__label {
      border-radius: 2em 0 0 2em;
      border-right: 0;
    }
  }
  &:last-of-type {
    .form-check__label {
      border-radius: 0 2em 2em 0;
      border-left: 0;
    }
  }
  .form-check__input:checked + .form-check__label {
    border-color: map-get($formStates, --checked-border-color);
    background-color: transparent;
    &::after {
      content: "";
      position: absolute;
      left: 0;
      right: 0;
      top: 0;
      bottom: 0;
      z-index: -1;
      border-radius: 2em;
      background-color: map-get($formStates, --checked-background-color);
    }
  }
}

// icon add
.form-check-button--switch {
  &[data-row="TOP"] {
    .form-check__label {
      &::before {
        @include svgIconOption(thumbs-up, 999);
        margin-right: 0.2rem;
      }
    }
    .form-check__input:checked ~ label::before {
      @include svgIconOption(thumbs-up, fff);
    }
  }
  &[data-row="HOT"] {
    .form-check__label {
      &::before {
        @include svgIconOption(fire, 999);
        margin-right: 0.2rem;
      }
    }
    .form-check__input:checked ~ label::before {
      @include svgIconOption(fire, fff);
    }
  }
}

/*
■■■■■■■■■■■■■■■■■■■■■■■■■■■■■■■■■■■■■■■■■■■■■■■■■■■■■■■■■■■■■■■■■■■■■■■■■■■■■■■■■■■■■■■■■■■■■■■■■■
*/
