@charset "utf-8";

// Common UI
*[data-dg-type] {
  line-height: 1;
}

.ui_invisible {
  position: absolute !important;
  width: 0;
  height: 0;
  font-size: 0;
  overflow: hidden;
  visibility: hidden;
}

*[disabled],
.ui_disabled {
  // opacity: .7;
  pointer-events: none;

  & + label {
    border-color: #dadada;
    background: #efefef;
    pointer-events: none;
  }

  input {
    border-color: #dadada;
    background: #efefef;
  }

  label {
    border-color: #dadada;
    background: #efefef;
  }
}

.ui_wid_100p {
  width: 100% !important;
  box-sizing: border-box;
}

.ui_al {
  // 텍스트 정렬(좌)
  text-align: left !important;
}

.ui_ac {
  // 텍스트 정렬(중)
  text-align: center !important;
}

.ui_ar {
  // 텍스트 정렬(우)
  text-align: right !important;
}

.ui_vt {
  // 상하 정렬(상)
  vertical-align: top !important;
}

.ui_vm {
  // 상하 정렬(Baseline)
  vertical-align: baseline !important;
}

.ui_vb {
  // 상하 정렬(하)
  vertical-align: bottom !important;
}

.ui_autowrap {
  line-height: 100% !important;
  overflow: visible !important;
  white-space: normal !important;
}

// 링크 텍스트(언더라인 이펙트)
.ui_lnk {
  &:hover {
    text-decoration: underline;
  }
}

.ui_label {
  position: relative;
  display: inline-block;
  line-height: 22px;
  padding: 0 6px 0 22px;
  background: #f3f3f3;
  color: $component_font !important;
  border-radius: 100px;

  &:before {
    content: "";
    position: absolute;
    top: 5px;
    left: 5px;
    display: block;
    width: 12px;
    height: 12px;
    border-radius: 50%;
    background: #808080;
  }

  &.fc_positive {
    background: #ecf4fa;

    &:before {
      content: "";
      background-color: $font-senti-positive;
    }
  }

  &.fc_negative {
    background: #fcebeb;

    &:before {
      content: "";
      background-color: $font-senti-negative;
    }
  }

  &.fc_neutral {
    background: #f3f3f3;

    &:before {
      content: "";
      background-color: $font-senti-neutral;
    }
  }
}

// 아이콘
.ui_icon {
  font-family: "icomoon";
  font-size: 16px;
}

.ui_icon_img {
  position: relative;
  padding-left: 15px;

  &:before {
    content: "";
    position: absolute;
    top: 3px;
    left: 0;
    display: block;
    width: 14px;
    height: 14px;
    background: url(#{$G-img-path}/ui/ui_btns_icon.png) no-repeat 0 0;
  }

  &.top {
    &:before {
      background-position: -360px -0px;
    }
  }

  &.hot {
    &:before {
      background-position: -360px -25px;
    }
  }
}

// Bullet
.ui_bullet {
  @include inlineBlock();

  > * {
    display: block;
    width: 10px;
    height: 16px;
    margin: 0 5px 0 0;
    background: url(#{$G-img-path}/ui/ui_bullets.png) no-repeat 0 0;
    text-align: left;
    text-indent: -9999px;

    // 카페 아이콘
    &.cafe {
      width: 14px;
      background-position: -77px -16px;
    }
  }
}

// Chart
.ui_chart_wrap {
  min-height: 100px;
  font-size: 12px;

  // text {
  //     font-size: 12px;
  // }

  // .amcharts-balloon-div {
  //     * {
  //         font-size: 12px;
  //     }
  // }

  * {
    font-family: inherit !important;
    @include transition(0);
  }

  &:not(.is-not-hand) {
    .amcharts-pie-item,
    .amcharts-graph-column-element,
    .amcharts-graph-bullet,
    .amcharts-graph-fill,
    .axis_pointer .amcharts-axis-label {
      cursor: pointer;
    }
  }

  // 비교 차트인 경우 카테고리엑시스 비교기간 색상
  &.is-compare {
    .amcharts-category-axis {
      .amcharts-axis-label {
        tspan:last-child:not(:only-child) {
          fill: #c0c0c0;
        }
      }
    }
  }

  // 클라우드 차트
  .amcharts-WordCloud {
    .word_item {
      cursor: pointer !important;

      rect,
      text {
      }

      rect {
        fill-opacity: 0;
      }

      &:hover {
        rect {
          fill-opacity: 0.5;
          @include transition(0.24);
        }

        text {
          fill: #ffffff !important;
          @include transition(0.24);
        }
      }

      &.active {
        rect {
          fill-opacity: 1;
          @include transition(0.24);
        }

        text {
          fill: #ffffff !important;
          @include transition(0.24);
        }
      }
    }
  }

  // 트리맵 차트
  // .amcharts-NavigationBar-group {
  //     .amcharts-Label.amcharts-TextLink {
  //         tspan {
  //             fill: #ff0000 !important;
  //         }
  //     }
  // }
}

.chart-label {
  display: inline-block;
  padding: 0 13px;
  height: 30px;
  color: #505050;
  border-radius: 4px;
  line-height: 30px;

  &.color_01 {
    background: #88e2bb;
  }

  &.color_02 {
    background: #ede376;
  }

  &.color_03 {
    background: #f3be79;
  }
}

.ui_cloud_wrap {
  &.no_data {
    position: relative;

    > .ui_cloud {
      &:after {
        content: " ";
        position: absolute;
        top: 0;
        left: 0;
        display: block;
        width: 100%;
        height: 100%;
        text-align: center;
        background: url(#{$G-img-path}/ui/no_data_icon_01.png) no-repeat -150px 0;
        background-color: #ffffff;
        background-position: center;
      }
    }
  }
}

.ui_map_chart_container {
  position: relative;
  padding: 0 0 0 90px;
  box-sizing: border-box;
  overflow: hidden;

  > .legend {
    position: absolute;
    top: 0;
    left: 0;
    width: 150px;
    height: 100%;
  }

  > .ui_map_chart_wrap {
    position: absolute;
    top: 0;
    right: 0;
    width: calc(100% - 110px);
    height: 100%;
    z-index: 0;
    @include opacity(0);
  }

  > .btn_home {
    position: absolute;
    top: 0;
    right: 0;
  }
}

.chart_tooltip {
  color: #333333;
  padding: 5px;
  text-align: center;

  .title {
    display: block;
    color: #333333;
    padding-bottom: 5px;
    font-size: 1em;
    line-height: 1;
  }

  .dv {
    font-size: 1.2em;
  }

  .row {
    display: block;
    color: #333333;
  }

  .ui_fluc {
    padding-top: 10px;
    color: #333333;
    font-size: 1em;
    line-height: 1;
    font-weight: bold;

    &.new {
      color: #ea2f4e;
      font-weight: bold;
    }

    &:before {
      margin-top: -10px;
    }
  }
}

// No Data - 이미지 형태
.ui_no_data {
  background: url(#{$G-img-path}/ui/ui_nodata.png) no-repeat center center !important;

  * {
    display: none !important;
  }
}

// No Data - 텍스트형태
.ui_no_data_txt {
  position: relative;
  color: $font_color_dm;
  background: url(#{$G-img-path}/ui/ui_nodata_txt.png) no-repeat center center;

  * {
    display: none !important;
  }

  // padding: 0 0 0 18px;

  // &:before {
  // 	content: '';
  // 	position: absolute;
  // 	top: 50%;
  // 	left: 0;
  // 	width: 12px;
  // 	height: 12px;
  // 	margin: -6px 0 0 0;
  // 	background: url(#{$G-img-path}/ui/ui_icons.png) no-repeat -83px 0
  // }
}

// loader
.ui_loader_container {
  position: relative;

  &:before {
    content: "";
    position: absolute;
    top: 0;
    left: 0;
    width: 100%;
    height: 100%;
    border-radius: 4px;
    z-index: 1;
    visibility: hidden;
    @include backgroundAlpha(#ffffff, 0.8);
    @include opacity(0);
    @include transition(0.2);
  }

  &:after {
    content: "";
    display: block;
    position: absolute;
    top: 50%;
    left: 50%;
    width: 40px;
    height: 40px;
    margin: -20px 0 0 -20px;
    background: url(#{$G-img-path}/ui/ui_loader_00.gif) no-repeat center center;
    font-size: 10px;
    text-align: center;
    line-height: 40px;
    z-index: 1;
    visibility: hidden;
    @include opacity(0);
    @include transition(0.2);
  }

  &.is-loading {
    &:before,
    &:after {
      visibility: visible;
      @include opacity(1);
    }
  }
}

// Input(text/textarea)
.ui_input {
  width: 100%;
  height: 28px;
  border: 1px solid $component_brd_color;
  background: #ffffff;
  color: #505050;
  padding-left: 10px;
  border-radius: 5px;
  box-sizing: border-box;
  outline: none;
  // height: 26px;
  // padding: 0 10px;
  // border: 1px solid $component_brd_color;
  // background: #ffffff;
  // box-sizing: border-box;
  // line-height: 24px;
  // outline: none;
  @include transition();

  &.small {
    height: 22px;
    line-height: 20px;
  }

  &.big {
    height: 32px;
    line-height: 30px;
  }

  &:hover,
  &:focus {
    &:not([readonly]) {
      border: 1px solid $component_active_color;
    }
  }

  &[readonly] {
    background: #efefef;
  }
}

.ui_input_iufo {
  display: block;
  padding: 0 0 0 5px;
  color: #a1a1a1;
  font-size: 0.917em;
  line-height: inherit;

  &.small {
    line-height: 22px;
  }

  &.big {
    line-height: 32px;
  }
}

// Textarea
.ui_textarea {
  display: block;
  padding: 10px;
  border: 1px solid $component_brd_color;
  box-sizing: border-box;
  background: #ffffff;
  line-height: 1.6em;
  resize: none;
  outline: none;
  @include transition();

  &:hover,
  &:focus {
    border: 1px solid $point_color;
  }
}

// Paginate
.ui_paginate {
  position: relative;
  padding: 10px 0;
  font-size: 1px;
  line-height: 0;

  > * {
    font-size: 12em;
    line-height: 1;
  }

  .in_wrap {
    @include inlineBlock();

    &:after {
      @include clearBoth();
    }

    > * {
      position: relative;
      float: left;
      min-width: 25px;
      height: 25px;
      padding: 0 5px;
      color: $component_font;
      margin-right: 1px;
      line-height: 25px;
      font-size: 11px;
      box-sizing: border-box;
      @include transition();

      &:hover {
        background: #e9e9e9;
      }

      &.active {
        color: #fff !important;
        background: #424242;
        color: $component_active_color_hl;
      }

      &.page_prev {
        border: 1px solid #efefef;
        text-align: center;
        // text-indent: -9999px;

        &:after {
          content: "\e001";
          font-family: "icomoon";
          color: $component_font;
          font-size: 13px;
          line-height: 25px;
        }

        &.prev {
          &:after {
            content: "\e003";
          }
        }

        & + a {
          font-size: 14px;
        }

        &.ui_disabled {
          &:after {
            color: #cccdcd;
          }
        }
      }

      &.page_next {
        border: 1px solid #efefef;
        text-align: center;

        &:after {
          content: "\e000";
          font-family: "icomoon";
          font-size: 13px;
          line-height: 25px;
        }

        &.next {
          &:after {
            content: "\e002";
          }
        }

        &.ui_disabled {
          &:after {
            color: #cccdcd;
          }
        }
      }
    }
  }
}

// Fluc
.ui_fluc {
  @include inlineBlock();
  position: relative;
  line-height: 1;
  @include transition();

  &.is-box {
    padding: 0 5px;
    border-radius: 50px;
    background: #f0f0f0;
    line-height: 16px;
  }

  &.before {
    &.up:before {
      content: "\e086";
      font-family: "icomoon";
      color: #f32539;
    }

    &.dn:before {
      content: "\e087";
      font-family: "icomoon";
      color: #266ccc;
    }

    &.none:before {
      content: "- ";
      font-size: 1.2em;
      font-weight: bold;
      color: #898a8b;
    }
  }

  &.after {
    &.up:after {
      content: " ▲";
      color: #e41c1c;
    }

    &.dn:after {
      content: " ▼";
      color: #2d6ceb;
    }

    &.none:after {
      content: " -";
      font-size: 1.2em;
      font-weight: bold;
      color: #898a8b;
    }
  }
}

// UI - Section > Row > Col > Box
.ui_section {
  display: block;
  padding: 40px 0;

  > .wrap {
    width: $doc_wid;
    margin: 0 auto;

    > h3 {
      display: block;
      padding: 20px 0;
      color: #000000;
      font-size: 1.857em;
      font-weight: bold;
      line-height: 1;
      text-align: center;
    }
  }

  &:nth-child(even) {
    background: #eeeeee;
  }
}

.ui_row {
  position: relative;
  padding-bottom: 40px;
  //margin: 0 -10px;
  @include clearBoth();

  &:last-child {
    padding: 0 !important;
  }

  > .ui_col {
    position: relative;
    //padding: 0 10px;

    @for $Loop1 from 1 through 24 {
      &.w#{$Loop1} {
        float: left;
        width: (100% / 24) * $Loop1;
        box-sizing: border-box;

        &:last-child:not(:first-child) {
          float: right;
        }
      }
    }

    .ui_col {
      padding: 20px;
    }

    &.is-no-pad {
      padding: 0;
    }

    &.is-10-pad {
      padding: 10px;
    }

    &.is-15-pad {
      padding: 15px;
    }
  }

  .ui_row {
    // padding-top: 25px !important;
    // padding-bottom: 40px;

    &:last-child {
      padding-bottom: 0;
    }

    .ui_row {
      padding-bottom: 10px;

      &:last-child {
        padding-bottom: 0;
      }
    }
  }

  &.is-no-pad-top {
    padding-top: 0 !important;
  }

  // Padding 포함
  &.on_pad {
    margin: 0 -10px;

    > .ui_col {
      padding: 0 10px;
    }

    &.is-pad-05 {
      margin: 0 -4px;

      > .ui_col {
        padding: 0 4px;
      }
    }

    &.d-flex {
      > .ui_col {
        display: flex;
        justify-content: space-between;
        flex: 1;
        float: none;
        width: auto;
        > .wrap {
          display: flex;
          flex: 1;
          > .ui_box {
            display: flex;
            flex-direction: column;
            flex: 1;
            > .box_header {
              display: flex;
            }
            > .box_content {
              display: flex;
              flex: 1;
              align-items: center;
              > .wrap {
                display: flex;
                flex: 1;
                .ui_chart_wrap {
                  width: 100%;
                }
              }
            }
          }
        }
      }
    }
  }

  // Cols에 세로 보더 생성
  &.on_bd {
    .ui_col {
      border-right: 1px solid #eeeeee;
      box-sizing: border-box;

      &:last-child {
        border-right: none;
      }
    }
  }
}

.ui_box {
  position: relative;
  min-height: 90px;
  // background: #ffffff;
  box-sizing: border-box;
  @include transition();

  .box_header {
    position: relative;
    padding-bottom: 14px;
    // border-bottom: 1px solid #e1e1e1;

    &.is-no-brd {
      border-bottom: none;

      & + .box_content {
        padding-top: 0 !important;
      }
    }

    &.is-bd {
      border-bottom: 1px solid #eeeeee !important;
    }

    > h4 {
      position: relative;
      padding: 0;
      color: $font_color_hl2;
      font-size: 16px;
      font-weight: bold;
      line-height: 28px;
      @include transition();

      // &:before {
      //   content: "";
      //   position: absolute;
      //   top: 50%;
      //   left: -9px;
      //   width: 3px;
      //   height: 18px;
      //   background: $font_color_hl2;
      //   border-radius: 2px;
      //   margin-top: -9px;
      // }
    }

    > h5 {
      color: #000000;
      font-size: 1.286em;
      line-height: 30px;
      @include transition();
    }

    > .box_header_rc {
      position: absolute;
      top: 5px;
      right: 0px;
      line-height: 28px;
      @include clearBoth();

      > * {
        float: left;
        margin: 0 4px 0 0;

        &:last-child {
          margin-right: 0;
        }
      }
      button {
        border: 1px solid $component_brd_color;
      }
    }

    > .box_header_cc {
      padding: 10px 0;
      border-bottom: 1px solid #e9e9e9;
      text-align: center;

      > * {
        display: inline-block;
      }
    }

    em {
      font-size: 22px;
    }
  }

  .box_content_header {
    padding: 10px;
    background: #f9f9f9;
    line-height: 26px;
    @include clearBoth();

    &.bot_bd {
      border-bottom: 1px dashed #dfdfdf;
    }

    .lc {
      float: left;
    }

    .rc {
      float: right;
    }

    > * {
      @include clearBoth();

      > * {
        float: left;
        margin: 0 2px 0 0;

        &:last-child {
          margin-right: 0;
        }
      }
    }

    &.is-pad-05 {
      padding: 0 5px;
    }
  }

  .box_content {
    position: relative;
    padding: 10px 0;
    border: 1px solid $component_brd_color;
    border-radius: 15px;
    background-color: #fff;

    > .wrap {
      padding: 15px;

      &.is-2-depth {
        margin-top: 25px;
        padding: 0 !important;
        border: 1px solid #e1e1e1;
      }
    }

    &.is-no-pad-top {
      padding-top: 0;
    }

    &.is-no-pad {
      padding: 0;
    }

    &.is-top-pad {
      padding-top: 15px !important;
    }
  }

  .box_footer {
    padding: 0 20px 20px;

    .btns {
      text-align: center;
    }
  }

  &.dims {
    > .box_content {
      background: #fbfbfb;
    }
  }

  &.is-1-depth {
    .box_header {
      position: relative;
      padding: 15px 12px 8px !important;
      border-bottom: 2px solid #eeeeee !important;

      > .box_header_rc {
        top: 15px !important;
        right: 12px !important;
      }
    }

    .box_content {
      position: relative;
      padding: 12px !important;
    }
  }

  &.is-2-depth {
    background: #ffffff !important;
    padding: 28px 25px;

    .box_header {
      position: relative;
      padding: 12px 0 8px !important;
      border-bottom: 1px solid #e1e1e1 !important;

      > h5 {
        color: $font_color;
        font-size: 16px;
        line-height: 14px;
        @include clearBoth();

        > * {
          float: left;
        }

        em {
          display: inline-block;
          max-width: calc(100% - 300px);
          white-space: nowrap;
          color: #e60012;
          font-size: 16px;
          overflow: hidden;
          text-overflow: ellipsis;

          &:before {
            content: "\e000";
            color: $font_color;
            font-family: "icomoon";
            font-size: 13px;
            padding: 0 4px 0 5px;
          }
        }

        span {
          color: $font_color;
          font-size: 16px;
        }
      }

      > .box_header_rc {
        top: 3px !important;
        right: 0px !important;
      }
    }

    .box_content {
      position: relative;

      > .ui_function {
        margin-bottom: 20px;
      }
    }
  }

  &.is-3-depth {
    background: #fcfcfc !important;
    border: 1px solid #e6e6e6 !important;
    border-radius: 4px !important;

    .box_header {
      > h5 {
        color: #4f4f4f;
        font-size: 14px;

        em {
          color: #4f4f4f;
          font-size: 14px;
        }
      }

      > .box_header_rc {
        top: 8px;
        right: 8px;
      }
    }
  }

  .ui_box {
    border: none;
    background: none;

    .box_header {
      padding: 0 0 10px;
      border-bottom: none;

      > .box_header_rc {
        top: 0;
        right: 0;
      }

      &.no_pad {
        padding: 0;
      }
    }

    > .box_content {
      // padding: 0;
    }

    &.on_bd {
      border: 1px solid #e9e9e9 !important;
    }

    &.radi_00 {
      border-radius: 0;
    }
  }
}

.ui_bot_btns {
  padding: 15px 0;
  text-align: center;
}

.ui_toggle_box {
  @extend .ui_box; // ui_box 상속
  border: 1px solid $brd_color_hl;

  .box_header {
    padding: 10px 10px;
    border-bottom: none;
    background: $brd_color_hl;

    > h3,
    > h4,
    > h5,
    > h6 {
      color: #ffffff;
