}

.ui_helper {
  display: block;
  position: relative;
  padding: 20px 20px 20px 106px;
  border: 2px solid #e1e1e1;
  border-radius: 4px;
  background: #ededed;

  &:before {
    content: "\e006";
    position: absolute;
    top: 50%;
    left: 20px;
    width: 16px;
    height: 16px;
    margin: -8px 0 0 0;
    font-family: "icomoon";
    color: #c0c0c0;
    font-size: 16px;
  }

  &:after {
    content: "도움말";
    position: absolute;
    top: 50%;
    left: 40px;
    margin: -8px 0 0 0;
    color: #c0c0c0;
    font-weight: bold;
    line-height: 16px;
  }

  > ul {
    font-size: 0.857em;

    > li {
      position: relative;
      padding: 0 0 0 7px;
      line-height: 1.8em;

      &:before {
        content: "";
        position: absolute;
        top: 9px;
        left: 0;
        width: 2px;
        height: 2px;
        background: #909090;
      }

      > ul {
        padding: 0 0 3px 0;
        font-size: 0.917em;

        > li {
          position: relative;
          padding: 0 0 0 8px;
          line-height: 1.6em;

          &:before {
            content: "";
            position: absolute;
            top: 8px;
            left: 0;
            width: 4px;
            height: 1px;
            background: #909090;
          }
        }
      }
    }
  }
}

// 정렬(오름/내림) 버튼
.ui_sort {
  display: inline-block;
  position: relative;
  padding: 0 15px 0 0;
  cursor: pointer;

  .txt {
    display: block;
    line-height: 1.2em;
    z-index: 0;
  }

  input {
    @include uiComponentHidden();

    & + label {
      position: absolute;
      top: 0;
      left: 0;
      width: 100%;
      height: 100%;
      text-align: left;
      text-indent: -9999px;
      cursor: pointer;
      z-index: 2;

      &:before {
        content: "";
        position: absolute;
        top: 0;
        left: 0;
        width: 100%;
        height: 100%;
        background: rgba(0, 0, 0, 0.001);
      }
    }

    &:nth-child(4) + label {
      z-index: 1;
    }

    &:nth-child(2) + label:after {
      content: "";
      position: absolute;
      top: 50%;
      right: 0;
      width: 7px;
      height: 4px;
      margin-top: -6px;
      background: url(#{$G-img-path}/ui/ui_icons.png) no-repeat -128px 0;
    }

    &:nth-child(4) + label:after {
      content: "";
      position: absolute;
      top: 50%;
      right: 0;
      width: 7px;
      height: 4px;
      margin-top: 2px;
      background: url(#{$G-img-path}/ui/ui_icons.png) no-repeat -128px -6px;
    }

    &:checked {
      & + label {
        z-index: 1;
      }

      &:nth-child(2) + label:after {
        background-position: -151px 0;
      }

      &:nth-child(4) + label:after {
        background-position: -151px -6px;
      }
    }
  }
}

// 라디오 그룹
.ui_radio_group {
  line-height: 26px;
  @include clearBoth();

  > * {
    float: left;
    margin-right: 1px;
    border-left: none;

    &:first-child {
    }

    &:last-child {
      margin-right: 0;
    }

    > input[type="radio"] {
      @include customComponent();

      & + label {
        display: block;
        padding: 0 10px;
        background: #e8e8e8;
        color: #666666;
        cursor: pointer;
        @include transition();

        @at-root .wide#{&} {
          padding: 0 20px;
        }

        @at-root .big#{&} {
          padding: 0 20px;
        }
      }

      &:checked {
        & + label {
          background: #555555;
          color: #ffffff;
        }
      }
    }

    &:hover {
      input + label {
        background: #f0f0f0;
      }
    }
  }

  &.small {
    line-height: 20px !important;
  }

  &.big {
    line-height: 30px !important;
    font-size: 1.167em;
  }
}

// Tab
.ui_tab {
  > ul {
    @include clearBoth();

    > li {
      float: left;
      padding: 0 2px 0 0;

      &:first-child {
        label {
          border-radius: 4px 0 0 0;
        }
      }

      &:last-child {
        padding: 0;

        label {
          border-radius: 0 4px 0 0;
        }
      }

      > input {
        @include customComponent();

        & + label {
          display: block;
          padding: 7px 20px;
          background: #dddddd;
          font-size: 1.143em;
          line-height: 28px;
          cursor: pointer;
          @include transition();
        }

        &:hover {
          & + label {
            background: #bbbbbb;
            color: $font_color_hl;
          }
        }

        &:checked {
          & + label {
            background: #808080;
            color: #ffffff;
          }
        }
      }
    }
  }

  &.is-bd {
    border-bottom: 2px solid #555555;

    > ul {
      > li {
        > input {
          & + label {
            padding: 7px 20px;
            background: #f0f0f0;
            color: #999999;
            font-size: 1.167em;
            line-height: 1.2em;
            cursor: pointer;
            @include transition();
          }

          &:hover,
          &:checked {
            & + label {
              background: $point_color;
              color: #ffffff;
            }
          }
        }
      }
    }
  }

  &.content_tab {
    > ul {
      > li {
        padding-right: 2px;
        box-sizing: border-box;

        > input {
          & + label {
            padding: 10px 20px;
            font-size: 22px;
            text-align: center;
          }
        }

        &:last-child {
          padding-right: 0;
        }
      }
    }
  }
}

// 컴포넌트 그룹(타이틀:컴포넌트)
.ui_comp_group {
  @include clearBoth();

  > * {
    float: left;
