}

// In Search
.ui_in_search {
  @include inlineBlock();
  position: relative;

  input {
    display: block;
    width: 100%;
    height: 26px;
    padding: 0 30px 0 6px;
    border: 1px solid #e1e1e1;
    box-sizing: border-box;
    background: #ffffff;
    outline: none;
    @include transition(0.12, 0, border);

    &:focus {
      border: 1px solid $point_color;
    }
  }

  button {
    position: absolute;
    top: 1px;
    right: 1px;
    width: 24px;
    height: 24px;
    background: url(#{$G-img-path}/ui/ui_btns_icon.png) no-repeat -25px -20px;
    text-align: left;
    text-indent: -9999px;
  }

  &:hover {
    input {
      @extend :focus;
    }
  }

  &.is-confirm {
    input {
      padding-right: 6px;
      color: $point_color;

      &:focus {
        padding-right: 30px;

        & + label + button {
          display: block;
        }
      }
    }

    button {
      display: none;
      position: absolute;
      top: 1px;
      right: 1px;
      width: 24px;
      height: 24px;
      background: none;
      text-align: left;
      text-indent: -9999px;

      &:before {
        content: "\e053";
        position: absolute;
        top: 0;
        left: 0;
        width: 100%;
        height: 100%;
        font-family: "icomoon";
        text-align: center;
        text-indent: 0;
        line-height: 24px;
        @include transition();
      }

      &:hover {
        &:before {
          color: $point_color;
        }
      }
    }
  }
}

