// UI - Date Picker
.ui_datepicker {
  @include inlineBlock();
  position: relative;
  padding: 0 0 0 0;
  vertical-align: top;
  @include transition();
  @include clearBoth();

  > .input_wrap {
    @include clearBoth();

    > * {
      float: left;

      &.btn_month {
        position: relative;
        width: 26px;
        height: 26px;
        border: 1px solid $component_brd_color;
        background-color: #ffffff;
        box-sizing: border-box;
        @include transition();

        &:hover {
          //border: 1px solid $point_color;
          background: #f0f0f0;
        }

        > * {
          position: absolute;
          top: 50%;
          left: 50%;
          width: 10px;
          height: 10px;
          margin: -5px 0 0 -5px;
          @include textHidden();
        }

        &.btn_prv_month {
          border-radius: 4px 0 0 4px;
          border-right: none !important;

          > * {
            background: url(#{$G-img-path}/ui/ui_datepicker_arrow.png) no-repeat -138px 0;
          }

          &:hover {
            > * {
              background-position: -160px 0;
            }
          }
        }

        &.btn_nxt_month {
          border-radius: 0 4px 4px 0;
          border-left: none !important;

          > * {
            background: url(#{$G-img-path}/ui/ui_datepicker_arrow.png) no-repeat -182px 0;
          }

          &:hover {
            > * {
              background-position: -204px 0;
            }
          }
        }
      }

      &.input {
        position: relative;
        width: 108px;
        overflow: hidden;

        > .date_result {
          display: block;
          position: relative;
          width: 100%;
          height: 26px;
          padding: 0 0 0 10px;
          border: 1px solid $component_brd_color;
          box-sizing: border-box;
          font-size: 1.083em;
          line-height: 24px;
          cursor: pointer;
          @include transition();

          & + label {
            position: absolute;
            top: -107px;
            right: 2px;
            width: 28px;
            height: 300px;
            background: url(#{$G-img-path}/ui/ui_component.png) no-repeat right 0;
            cursor: pointer;
            @include transition();
          }

          &:hover,
          &.active {
            border: 1px solid #666666;

            & + label {
              top: -72px;
            }
          }
        }
      }
    }
  }

  .ui_calendar {
    display: none;
    position: fixed;
    margin-top: 27px;
    border: 1px solid #666666;
    z-index: 11;
  }

  &[data-time] {
    .input_wrap {
      input {
        width: 155px;
      }
    }
  }

  &[data-fixed="true"] {
    .ui_calendar {
      position: fixed !important;
    }
  }

  &.small {
    .input_wrap {
      input {
        height: 22px;
        line-height: 20px;

        & + label {
          margin: -2px 0 0 0;
        }
      }
    }

    .btn_month {
      width: 22px;
      height: 22px;
    }
  }

  &.big {
    .input_wrap {
      input {
        height: 32px;
        line-height: 30px;

        & + label {
          margin: 3px 0 0 0;
        }
      }
    }

    .btn_month {
      width: 32px;
      height: 32px;
    }
  }

  &.pos_r {
    .ui_calendar {
      right: 0;
    }
  }
}
