      border-right: none;
    }

    .ui_brd_list {
      float: right;
      border-bottom: 1px solid #e9e9e9;
    }
  }
}

.ui_rkeyword {
  padding: 10px;
  text-align: center;

  > .sort {
    @include inlineBlock();
    vertical-align: top;

    .dcp {
      // 스타일, 분위기, 색상, 행위, 공간, 긍정, 부정, 일반
      $colors: #cacb50, #64b3c1, #c87eda, #5072a8, #e08e1b, $font-senti-positive, $font-senti-negative, #b4b4b4;
      $colors_bg: #fdfde2, #edfcff, #fdf3ff, #e9f0fa, #fff5e6, #e6eff7, #ffe9e9, #f5f5f5;

      @each $color in $colors {
        $idx: index($colors, $color);

        &.is-attr-0#{$idx} {
          input {
            & + label {
              color: $color;
            }

            &:checked {
              & + label {
                border-color: $color;
                background: $color;
                color: #ffffff;
              }
            }

            &:not(:checked) + label {
              &:hover {
                border-color: $color;
                background: nth($colors_bg, $idx);
              }
            }
          }
        }
      }
    }

    .grp {
      margin-right: 10px;
      @include clearBoth();

      &:last-child {
        margin: 0;
      }

      > * {
        float: left;
        position: relative;

        label {
          border-radius: 0;
        }

        &:first-child {
          label {
            border-radius: 4px 0 0 4px;
          }
        }

        &:last-child {
          label {
            border-radius: 0 4px 4px 0;
          }
        }

        &:not(:first-child) {
          margin-left: -1px;
        }

        &:hover {
          z-index: 1;
        }
      }
    }
  }

  > .wrap {
    padding: 40px 0 20px;
  }
}

// 차트 감성 범례
.ui_senti_gage {
  @include inlineBlock();
  position: relative;
  white-space: nowrap;

  > * {
    @include inlineBlock();
    position: relative;
    height: 16px;
    min-width: 5px;

    &.positive {
      background: #5ba1e0;
    }

    &.negative {
      background: #ea7070;
    }

    &.neutral {
      background: #aaaaaa;
    }

    > button {
      position: relative;
      width: 100%;
      height: 100%;
      text-align: left;
      text-indent: -9999px;
    }
  }
}

// Bubble Text
.ui_bubble_txt {
  display: block;
  overflow: hidden;
  text-overflow: ellipsis;
  white-space: nowrap;
}

//색상 라벨
.ui_square_label {
  position: relative;
  line-height: 1;

  &.before {
    padding-left: 14px;

    &:before {
      content: "";
      position: absolute;
      top: 50%;
      left: 0;
      display: block;
      width: 8px;
      height: 8px;
      margin-top: -4px;
    }
  }

  &.color_01 {
    &:before {
      background: #88e2bb;
    }
  }

  &.color_02 {
    &:before {
      background: #ede376;
    }
  }

  &.color_03 {
    &:before {
      background: #f3be79;
    }
  }
}
