/* SCSS Document */
@charset "UTF-8";

/****************************************************************************
    Breakpoints
****************************************************************************/
@mixin sm {
  @media (min-width: #{$G-breakpoint-medium - 1px}) {
    @content;
  }
}

@mixin md {
  @media (min-width: #{$G-breakpoint-medium}) {
    @content;
  }
}

@mixin lg {
  @media (min-width: #{$G-breakpoint-large}) {
    @content;
  }
}

@mixin xl {
  @media (min-width: #{$G-breakpoint-extraLarge}) {
    @content;
  }
}

@mixin xxl {
  @media (min-width: #{$G-breakpoint-extraExtraLarge}) {
    @content;
  }
}

@mixin fhd {
  @media (min-width: #{$G-breakpoint-fhd}) {
    @content;
  }
}

@mixin qhd {
  @media (min-width: #{$G-breakpoint-qhd}) {
    @content;
  }
}

@mixin thd {
  @media (min-width: #{$G-breakpoint-thd}) {
    @content;
  }
}

@mixin uhd {
  @media (min-width: #{$G-breakpoint-uhd}) {
    @content;
  }
}

/****************************************************************************
    브라우저 hack
****************************************************************************/
/*
    [style 작성 예]
        .선택자명 {
            @include 지시자명 {
                display:none;
            }
        }
*/

@mixin ieHack() {
  @media all and (-ms-high-contrast: none), (-ms-high-contrast: active) {
    @content;
  }
}

/* Safari 10.1+ */
@mixin safariHack() {
  @media not all and (min-resolution: 0.001dpcm) {
    @supports (-webkit-appearance: none) {
      @content;
    }
  }
}

/****************************************************************************
    Opacity
****************************************************************************/
@mixin opacity($opacity) {
  opacity: $opacity;
  $opacity: $opacity * 100;
  -ms-filter: "progid:DXImageTransform.Microsoft.Alpha(Opacity=" + $opacity + ")";
}

/****************************************************************************
    Background Alpha(RGBA)
****************************************************************************/
@mixin backgroundAlpha($color, $alpha) {
  $rgba: rgba($color, $alpha);
  $ie-hex-str: ie-hex-str($rgba);
  background-color: transparent;
  background-color: $rgba;
  filter: progid:DXImageTransform.Microsoft.gradient(startColorstr=#{$ie-hex-str},endColorstr=#{$ie-hex-str});
  zoom: 1;
}

/****************************************************************************
    Text Over시 Underline( 언더라인 색상 )
****************************************************************************/
@mixin overUnderline($color: #000000, $over_color: #000000, $active_color: #000000, $active: true) {
  > * {
    position: relative;

    &:after {
      content: "";
      position: absolute;
      bottom: -1px;
      left: 50%;
      right: 50%;
      height: 0;
      background: $color;

      @include transition(0.16);
    }
  }

  &:hover {
    > *:after {
      left: 0;
      right: 0;
      height: 1px;
      background: $over_color;
    }
  }

  &.active {
    > *:after {
      @if ($active == true) {
        left: 0;
        right: 0;
        height: 1px;
      }
      background: $active_color;
    }
  }
}

/****************************************************************************
    전체 옵션 트랜지션( 시간 )
****************************************************************************/
@mixin transition($time: 0.18, $delay: 0, $tgs: all, $easing: cubic-bezier(0.5, 0, 0.5, 1), $important: false) {
  $delay: $delay + s;
  $time: $time + s;

  @if ($important == false) {
    $important: null;
  } @else if ($important == true) {
    $important: !important;
  }

  -webkit-transition-property: unquote($tgs) $important;
  -webkit-transition-duration: $time $important;
  -webkit-transition-delay: $delay $important;
  -webkit-transition-timing-function: $easing $important;
  transition-property: unquote($tgs) $important;
  transition-duration: $time $important;
  transition-delay: $delay $important;
  transition-timing-function: $easing $important;
}

/****************************************************************************
    display
****************************************************************************/
@mixin inlineBlock {
  display: inline-block;
}

/****************************************************************************
    Float Clear
****************************************************************************/
@mixin clearBoth {
  &:after {
    content: "";
    display: block;
    clear: both;
  }
}

/****************************************************************************
    접근성 고려하여 시각적으로 숨기면서 화면 리더 사용 가능함 
****************************************************************************/
@mixin visuallyHidden {
  position: absolute;
  width: 1px;
  height: 1px;
  padding: 0;
  margin: -1px;
  overflow: hidden;
  clip: rect(0, 0, 0, 0);
  white-space: nowrap;
  border: 0;
}

/****************************************************************************
    SVG Icon 기본설정
****************************************************************************/
@mixin svgIconOption($icon: check, $color: fff) {
  content: "";
  display: inline-block;
  width: 1em;
  height: 1em;
  background-image: svgIcon($icon, $color);
  background-position: center center;
  background-repeat: no-repeat;
  background-size: contain;

  @include transition;
}

/****************************************************************************
     clamp (말줄임)
****************************************************************************/
@mixin clamp($lines: 1, $lineHeight: false) {
  display: -webkit-box !important;
  -webkit-line-clamp: $lines !important;
  -webkit-box-orient: vertical !important;
  word-break: break-all !important;
  white-space: normal !important;
  @if ($lineHeight) {
    height: #{$lineHeight * $lines};
    line-height: $lineHeight;
  }
  overflow: hidden !important;
  text-overflow: ellipsis !important;
}
