@charset "utf-8";

// Popup
#popup_container {
  display: none;
  position: fixed;
  top: 0;
  left: 0;
  width: 100%;
  min-width: 320px;
  height: 100%;
  z-index: 30;

  > .bg {
    display: block;
    width: 100%;
    height: 100%;
    @include backgroundAlpha(#000000, 0.55);
  }

  @at-root .popup_item {
    display: none;
    // display: block;
    position: absolute;
    top: 0;
    left: 0;
    min-width: 200px;
    max-width: 1200px;
    letter-spacing: -0.03em;
    // min-height: 200px;
    // max-height: 90%;
    // padding: 40px;
    border-radius: 14px;
    box-sizing: border-box;
    background: #ffffff;
    // overflow-y: auto;
    z-index: 30;

    .pop-table {
      thead {
        tr th {
          background: #dedede !important;
        }
      }
      tbody {
        td:nth-child(3) {
          padding: 0 !important;
        }
        > tr {
          &:nth-of-type(2n) {
            > th,
            > td {
              background: #ededef !important;
              border-color: #f4f4f4 !important;
            }
          }
          &:nth-of-type(4n + 1) {
            background-color: #fff;
            @include transition($tgs: background, $time: 0.5);
          }
        }
      }
    }
    > .header {
      position: relative;

      > .header-title {
        display: block;
        height: 50px;
        padding: 0 20px !important;
        background: #ffffff;
        border-bottom: 1px solid #ededef;
        box-sizing: border-box;
        color: $font_color_hl2 !important;
        border-radius: 13px 13px 0 0;
        font-size: 14px !important;
        font-weight: bold;
        text-align: left;
        line-height: 50px;
      }

      > .close {
        display: block;
        position: absolute;
        top: 11px;
        right: 15px;
        background: #fff;

        > .icon {
          font-size: 19px;
          color: #a9aaac;
        }
      }
    }

    > .content {
      > .wrap {
        position: relative;
        padding: 15px;
        border-radius: 0 0 13px 13px;
        box-sizing: border-box;

        .page_info > .cnt {
          color: #333333;
        }
        .footer {
          padding: 30px 0;
          // margin-top: 30px;

          .c-pagination__item {
            button {
              font-size: 12px;
              width: 28px;
              height: 28px;
            }
          }
        }

        .ui_box {
          > .box_header {
            padding: 10px 0;

            > .box_header_rc {
              line-height: 16px;
              label {
                font-size: 12px !important;
                width: 66px;
                height: 28px;
              }
              > * {
                margin: 0 1px 0 0;
              }
            }
          }

          > .box_content {
            padding: 0;
          }
        }
      }
    }
  }

  // 팝업 강제 활성을 위한 코드
  @at-root .popup_items .popup_item {
    position: relative;
  }
}
