/* SCSS Document */
@charset "UTF-8";

/****************************************************************************
    table                                  https://getbootstrap.com 참고함
****************************************************************************/
/* size */
$form-font-size: 1.2rem;

/*
■■■■■■■■■■■■■■■■■■■■■■■■■■■■■■■■■■■■■■■■■■■■■■■■■■■■■■■■■■■■■■■■■■■■■■■■■■■■■■■■■■■■■■■■■■■■■■■■■■
*/

/* 기본 */
.c-table {
  position: relative;
  width: 100%;
  //border-bottom: 1px solid #dedede;
  font-size: $form-font-size;
  th,
  td {
    white-space: -moz-pre-wrap !important; /* Mozilla, since 1999 */
    white-space: -webkit-pre-wrap; /* Chrome & Safari */
    white-space: -pre-wrap; /* Opera 4-6 */
    white-space: -o-pre-wrap; /* Opera 7 */
    white-space: pre-wrap; /* CSS3 */
    word-wrap: break-word; /* Internet Explorer 5.5+ */
    word-break: break-all;
    white-space: normal;
  }
  thead {
    tr {
      th {
        position: relative;
        padding: fz-in-em(11, $form-font-size);
        border-left: 1px solid #fff;
        font-weight: 400;
        background-color: #dedede;

        &:first-of-type {
          border-left: 0;
        }
        &[colspan] {
          border-bottom: 1px solid #fff;
        }
        .tit {
          padding-right: 1.4rem;
        }
      }
      ~ tr th {
        &:first-of-type {
          border-left: 1px solid #fff;
        }
      }
      .form-fieldset {
        display: flex;
        position: absolute;
        top: 0;
        right: 0;
        z-index: 1;
        width: 1.4rem;
        min-width: auto;
        height: 100%;
        .form-check {
          position: absolute;
          left: 0;
          width: 100%;
          height: 50%;
          &[data-row="오름차순"] {
            top: 0;
            .form-check__input {
              background-image: svgIcon(chevron-up, A9AAAC);
              &:hover,
              &:checked {
                background-image: svgIcon(chevron-up, fff);
              }
            }
          }
          &[data-row="내림차순"] {
            bottom: 0;
            .form-check__input {
              background-image: svgIcon(chevron-down, A9AAAC);
              &:hover,
              &:checked {
                background-image: svgIcon(chevron-down, fff);
              }
            }
          }
          &__input {
            height: 100%;
            border: 0;
            border-radius: 0;
            background-color: $G-hover-background-color;
            background-size: auto 0.4rem;
            &:hover,
            &:checked {
              background-color: $G-checked-background-color;
            }
          }
        }
      }
    }
    &.is-sticky {
      tr {
        th {
          position: sticky;
          top: 0;
          z-index: 10;
        }
      }
    }
  }
  tbody {
    tr {
      th,
      td {
        height: 46.38px;
        padding: fz-in-em(11, $form-font-size);
        border-top: 1px solid $G-color-border;
        border-left: 1px solid $G-color-border;
        word-break: keep-all;
        @include transition;
        &:first-of-type {
          border-left: 0;
        }
        .link {
          &:hover {
            text-decoration: underline;
          }
        }
      }
      &:nth-of-type(even) {
        > th,
        > td {
          border-color: #f4f4f4;
          background-color: #ededef;
        }
      }
      &:hover {
        th,
        td {
          border-color: #fff;
          background-color: #e8e4e2;
        }
      }

      &.tr_active{
        td{
          border-color: #fff;
          background-color: #e8e4e2;
        }
      }
    }
  }

  // 감성
  .icon-circle {
    @include iconSvgOption(circle, var(--color-set));
  }
}

/*
■■■■■■■■■■■■■■■■■■■■■■■■■■■■■■■■■■■■■■■■■■■■■■■■■■■■■■■■■■■■■■■■■■■■■■■■■■■■■■■■■■■■■■■■■■■■■■■■■■
*/

/* SNS 분석 게시판 */
.c-table-sns {
  $ta: c-table-sns;
  $su: summary;
  $de: detail;
  $se: section;

  thead {
    &.is-sticky {
      tr:nth-of-type(2) {
        th {
          top: 3.85rem;
        }
      }
    }
    // @at-root #header.is-fixed ~ #container .#{$ta} thead tr:nth-of-type(2) th {
    //   top: $G-header-height + 6rem + 3.8rem  !important;
    // }
  }

  // 요약정보 - tr, td
  .summary {
    > th,
    > td {
      padding: 0.5rem 1rem;
    }
    &:hover,
    &--is-active {
      > th,
      > td {
        background-color: #e8e4e2 !important;
        border-color: #fff !important;
      }
    }
    &:nth-of-type(2n + 1) {
      > th,
      > td {
        border-color: #f4f4f4;
        background-color: #ededef;
      }
    }
    &:nth-of-type(4n + 1) {
      > th,
      > td {
        background-color: #fff;
      }
    }

    // 요약정보 - user
    .user {
      @include clearBoth;
      .img {
        float: left;
        width: 4rem;
        height: 4rem;
        margin-right: 0.5rem;
        border-radius: 100%;
      }
      .name {
        padding-top: 0.4rem;
        font-weight: 700;
        line-height: 1em;
        cursor: pointer;
      }
      .id {
        padding-top: 0.7rem;
        color: #999;
        line-height: 1em;
        &::before {
          content: "@";
        }
      }
    }
  }

  // 상세내용 - tr, td
  .detail {
    @include transition($tgs: background);
    > td {
      overflow: hidden;
      padding: 2rem 1rem 2rem 2rem;
      border: 1px solid #fff;
      @include transition($tgs: height padding, $time: 0.5);
    }
    &:hover > td {
      background-color: #e8e4e2;
    }
    @at-root .#{$ta} .#{$su}--is-active + .#{$de} > td {
      background-color: #e8e4e2;
    }
    @at-root .#{$ta} .#{$su}:not(.#{$su}--is-active) + .#{$de} {
      > td {
        height: 0;
        padding-top: 0;
        padding-bottom: 0;
      }
    }
  }

  // 상세내용 - viewer
  .l-viewer {
    overflow: hidden;
    display: flex;
    flex-direction: row;
    height: 46rem;

    @include transition($tgs: height, $time: 0.5);
    @at-root .#{$ta} .#{$su}:not(.#{$su}--is-active) + .#{$de} {
      .l-viewer {
        height: 0;
      }
    }
    &-header {
      width: 28rem;
      margin-right: 1rem;
      background-color: #fff;
      .profile {
        height: 21.4rem;
        padding-top: 3.9rem;
        background-color: $G-color-secondary;
        &__photo-box {
          position: relative;
          width: 6rem;
          height: 6rem;
          margin: 0 auto 1.5rem;
          [class*="icon"] {
            position: absolute;
            left: 0;
            bottom: 0;
            font-size: 1.6rem;
          }
          .img {
            width: 100%;
            height: auto;
            border-radius: 100%;
          }
        }
        .name {
          font-weight: 700;
          color: #fff;
          text-align: center;
        }
        .id {
          color: #fff;
          text-align: center;
          margin-bottom: 1.5rem;
          &::before {
            content: "@";
          }
        }
        &__info-list {
          display: flex;
          flex-wrap: wrap;
          justify-content: space-between;
          width: 12.8rem;
          margin: 0 auto;
          > * {
            flex: 1 1 40%;
            color: #fff;
          }
          .count {
            font-weight: bold;
            text-align: right;
          }
        }
      }
      .graph-list {
        display: flex;
        justify-content: space-between;
        padding: 1.5rem 1rem 0;
        margin: 0 1rem;
        margin-bottom: 6.2rem;
        border-bottom: 1px solid $G-color-border;
        &__item {
          position: relative;
          width: 4.2rem;
          height: 5.4rem;
          .graph {
            overflow: hidden;
            position: absolute;
            left: 0;
            bottom: 0;
            width: 100%;
            &::after {
              content: "";
              position: absolute;
              left: 0;
              bottom: 0;
              width: 100%;
              height: 100%;
              background-color: var(--color-set, $G-disable-background-color);
              transform: translateY(0);
              @include transition($time: 1.2, $delay: 0.5);

              @at-root .#{$ta} .#{$su}:not(.#{$su}--is-active) + .#{$de} {
                .graph::after {
                  transform: translateY(100%);
                  @include transition($time: 0.5);
                }
              }
            }
          }
          .tit {
            position: absolute;
            left: 50%;
            bottom: -6.2rem;
            width: 100%;
            height: 6.2rem;
            padding-top: 0.5rem;
            font-size: 1.1rem;
            color: #666;
            transform: translateX(-50%);
            text-align: center;
          }
        }
      }
      .keyword {
        height: 10rem;
        margin: 0 1rem;
        background-color: #f4f4f4;
        .tit {
          display: flex;
          justify-content: center;
          align-items: center;
          height: 2.3rem;
          margin-bottom: 1rem;
          font-size: 1.3rem;
          font-weight: bold;
          background-color: #ededef;
        }
        &-list {
          height: 5.8rem;
          overflow: hidden;
          display: flex;
          justify-content: center;
          flex-wrap: wrap;
          .c-badge {
            margin: 0.25rem;
          }
        }
      }
    }
    &-body {
      overflow-y: scroll;
      display: inline-flex;
      flex: 1;
      flex-flow: column;
      padding-right: 1rem;
      .section {
        position: relative;
        width: 100%;
        padding: 1rem;
        margin-top: 1rem;
        border: 1px solid #fff;
        background-color: #fff;
        @include transition;
        &:hover {
          border-color: $G-checked-background-color;
        }
        &:first-of-type {
          margin-top: 0;
        }
        &__headline {
          overflow: auto;
          max-height: 9.2rem;
          margin-right: 5.4rem;
          margin-bottom: 1rem;
          line-height: 1.5em;
          cursor: pointer;
          @include transition($tgs: height, $time: 0.5);
          @at-root .#{$ta} .#{$se}:not(.#{$se}--is-active) {
            .#{$se}__headline {
              @include clamp(2);

              overflow: hidden;
              max-height: 3.4rem;
            }
          }
        }
        &__info-list {
          display: flex;
          align-items: center;
          &__item {
            display: inline-flex;
            position: relative;
            align-items: center;
            padding: 0 1rem;
            border-left: 1px solid $G-color-border;
            color: #666;
            &:first-of-type {
              border-left: 0;
            }
            &--news{
              cursor: pointer;
            }
            &--date {
              display: flex;
              margin-left: auto;
              border-left: 0;
              color: #999;
            }
            .c-badge {
              margin-left: 0.5rem;
              &:first-of-type {
                margin-left: 0;
              }
            }
            .icon-memo {
              margin-right: 0.3rem;
              font-size: 0.9em;
            }
            .tit {
              margin-right: 0.5rem;
              line-height: 1em;
            }
            .count {
              line-height: 1em;
            }
            &--news {
              // cursor: pointer;

              @mixin underline {
                &::after {
                  content: "";
                  position: absolute;
                  left: 2.5rem;
                  bottom: 0;
                  width: calc(100% - 3.5rem);
                  border-bottom: 1px solid $G-color-default;
                }
              }

              &:hover {
                @include underline;
              }
              @at-root .#{$ta} .#{$se}.#{$se}--is-active .#{$se}__info-list__item--news {
                @include underline;
              }
            }
          }
        }
        &__news-list {
          padding: 1rem 1rem 0 1rem;
          @include transition($time: 0.5);
          &__item {
            overflow: hidden;
            display: flex;
            align-items: center;
            position: relative;
            z-index: 1;
            padding: 0.8rem 1rem;
            margin-bottom: 0.5rem;
            background-color: #f4f4f4;
            @include transition($time: 0.5);
            @at-root .#{$ta} .#{$se}--is-active:hover .section__news-list__item {
              overflow: visible;
            }
            &:last-of-type {
              margin-bottom: 0;
            }
            .icon-memo {
              margin-right: 0.5rem;
            }
            .link {
              display: inline-block;
              position: relative;
              width: 74rem;
              font-weight: 700;
            }
            .source {
              max-width: 12rem;
              padding-right: 0.7rem;
              border-right: 1px solid $G-color-border;
              color: #999;
              line-height: 1em;
            }
            .source__txt {
              cursor: auto;
            }
            .date {
              padding-left: 0.7rem;
              color: #999;
              line-height: 1em;
            }
          }
          @at-root .#{$ta} .#{$se}:not(.#{$se}--is-active) {
            .#{$se}__news-list {
              padding-top: 0;
              &__item {
                padding-top: 0;
                padding-bottom: 0;
                height: 0;
                margin-bottom: 0;
              }
            }
          }
        }
      }
    }
  }

  // 상세보기
  .btn-detail {
    @include iconSvgOption(close, #a9aaac);

    display: flex;
    margin: 0 auto;
    width: 2.8rem;
    height: 2.8rem;
    border: 1px solid $G-color-border;
    border-radius: 0.4rem;
    font-size: 1rem;
    background-color: $G-checked-background-color;
    @include transition($time: 0.5);
    &::after {
      @include transition($time: 0.5);
      background-color: #fff;
    }
    @at-root .#{$ta} .#{$su}:not(.#{$su}--is-active) .btn-detail {
      background-color: #fff;
      &:hover {
        background-color: $G-checked-background-color;
        &::after {
          background-color: #fff !important;
        }
      }
      &::after {
        background-color: #a9aaac;
        transform: rotate(135deg);
      }
    }
  }

  // 게시글보기
  .btn-viewer {
    @include iconSvgOption(chevron-up, #a9aaac);

    position: absolute;
    top: 1rem;
    right: 2rem;
    display: flex;
    margin: 0 auto;
    width: 2.4rem;
    height: 2.4rem;
    border: 1px solid $G-color-border;
    border-radius: 0.4rem;
    font-size: 1rem;
    background-color: $G-checked-background-color;
    @include transition($time: 0.5);
    &::after {
      @include transition($time: 0.5);
      background-color: #fff;
    }
    @at-root .#{$ta} .#{$se}:not(.#{$se}--is-active) .btn-viewer {
      background-color: #fff;
      &:hover {
        background-color: $G-checked-background-color;
        &::after {
          background-color: #fff !important;
        }
      }
      &::after {
        background-color: #a9aaac;
        transform: rotate(180deg);
      }
    }
  }

  // 트위터
  .icon-twitter {
    @include iconSvgOption(twitter, #fff);

    display: inline-flex;
    align-items: center;
    justify-content: center;
    width: 1em;
    height: 1em;
    border-radius: 100%;
    font-size: 4rem;
    background-color: #55acee;
    &::after {
      font-size: 50%;
    }
  }

  // 페이스북 - typographic
  .icon-facebook {
    @include iconSvgOption(facebook, #fff);

    display: inline-flex;
    align-items: center;
    justify-content: center;
    width: 1em;
    height: 1em;
    border-radius: 100%;
    font-size: 4rem;
    background-color: #3c5a99;
    &::after {
      font-size: 50%;
    }
  }

  // 메모
  .icon-memo {
    @include iconSvgOption(memo, #cfcfcf);
  }
}

/*
■■■■■■■■■■■■■■■■■■■■■■■■■■■■■■■■■■■■■■■■■■■■■■■■■■■■■■■■■■■■■■■■■■■■■■■■■■■■■■■■■■■■■■■■■■■■■■■■■■
*/

.c-table-notys {
  table-layout: auto;
  thead {
    th {
      width: 150px !important;
    }
  }

  .ui_btn {
    width: 100px !important;
  }
}
