/* SCSS Document */
@charset "UTF-8";

/****************************************************************************
    Amcharts
****************************************************************************/
.amcharts-WordCloud {
  .word_item {
    rect {
      fill-opacity: 0;
    }
    &:hover {
      rect {
        fill-opacity: 0.5;
        @include transition(0.24);
      }

      text {
        fill: #ffffff !important;
        @include transition(0.24);
      }
    }
    &.active {
      rect {
        fill-opacity: 1;
        @include transition(0.24);
      }
      text {
        fill: #ffffff !important;
        @include transition(0.24);
      }
    }
  }
}

/****************************************************************************
    no Data
****************************************************************************/
[data-is-empty="true"],
.is-nodata {
  background: #fff url(#{$G-img-path}/common/nodata.png) 0 0 no-repeat !important;
  background-position: 50% 50% !important;
  background-size: 6rem !important;
  > * {
    opacity: 0 !important;
  }
  background-color: #fafafa !important;
}
