/* SCSS Document */
@charset "UTF-8";

/****************************************************************************
    Frame - 기본구조정의
****************************************************************************/
:root {
  --base-font-szie: 10px;
}

html,
body {
  width: 100%;
  height: 100%;
  font-size: calc(var(--base-font-szie) - 2px);
  @include xxl {
    font-size: calc(var(--base-font-szie) - 1px);
  }
  @include fhd {
    font-size: var(--base-font-szie);
  }
  @include qhd {
    font-size: calc(var(--base-font-szie) + 2px);
  }
  @include thd {
    font-size: calc(var(--base-font-szie) + 4px);
  }
  @include uhd {
    font-size: calc(var(--base-font-szie) + 10px);
  }
}

#wrap {
  position: relative;
  width: 100%;
  min-width: $G-inner-width;
  font-family: $G-font-family;
  font-size: $G-font-size;
  color: $G-color-default;
  letter-spacing: $G-letter-spacing;

  //확대 축소를 막기
  &[user-scalable='false'],
  &[user-scalable='false'] ~ * * {
    touch-action: pan-y;
  }
  // @include transition($time: 0.2, $tgs: opacity, $easing: linear);
}

#header {
  position: relative;
  z-index: 30;
  height: $G-header-height;

  // header fixed
  [data-header-fixed='true'] & {
    position: sticky;
    top: 0;
  }
}

#container {
  position: relative;
  z-index: 20;
  background-color: #fff;
}

#footer {
  position: relative;
  z-index: 10;
  height: 13rem;
}

.inner {
  position: relative;
  z-index: 1;
  width: $G-inner-width;
  height: 100%;
  margin: 0 auto;
  @include clearBoth;
}
