@charset "utf-8";

/****************************************************************************
  회원정보 한정 스타일
***************************************************************************/
#container.member {
  display: flex;
  align-items: center;
  min-height: 100vh;
  /*
  ■■■■■■■■■■■■■■■■■■■■■■■■■■■■■■■■■■■■■■■■■■■■■■■■■■■■■■■■■■■■■■■■■■■■■■■■■■■■■■■■■■■■■■■■■■■■■■■■■■
  */

  /* 로그인  */
  .l-member {
    width: 36rem;
    margin: 0 auto;
    &-header {
      &__title {
        margin-bottom: 2rem;
        color: #333333;
        font-size: 4rem;
        font-weight: bold;
        line-height: 1;
      }
    }
    .form {
      &-row {
        margin-bottom: 1.5rem;
        &[data-row="id"] {
          margin-bottom: 3rem;
          &:before {
            content: "";
            position: absolute;
            left: 1rem;
            top: 50%;
            height: 1.3rem;
            width: 1.3rem;
            background-image: svgIcon(user, 999);
            background-position: center center;
            background-size: contain;
            transform: translateY(-50%);
          }
          input {
            & + label {
              position: absolute;
              left: 0;
              bottom: -2.6rem;
              font-size: 1.2rem;
              color: #f86666;
            }

            &:valid {
              & + label {
                display: none;
              }
            }
          }

          .form-text__input {
            padding-left: 3.5rem;
          }
        }
        &[data-row="password"] {
          &:before {
            content: "";
            position: absolute;
            left: 1rem;
            top: 50%;
            height: 1.3rem;
            width: 1.3rem;
            background-image: svgIcon(lock, 999);
            background-position: center center;
            background-size: contain;
            transform: translateY(-50%);
          }
          .form-text__input {
            padding-left: 3.5rem;
            padding-right: 3.5rem;
          }
          .btn-eye {
            position: absolute;
            right: 1rem;
            top: 50%;
            width: 2.3rem;
            height: 2.3rem;
            background-image: svgIcon(eye, 999);
            background-position: center center;
            background-size: contain;
            transform: translateY(-50%);
            &--is-active {
              background-image: svgIcon(eye-slash, D9D9D9);
            }
          }
        }
        &[data-row="remember"] {
          .form-check__input {
            width: 1.8rem;
            height: 1.8rem;
            border-radius: 0.2rem;
            margin-right: 0.6rem;

            &:checked {
              background-color: #424242;
              background-size: 0.88333em;
            }
          }
          .form-check__label {
            color: #999999;
          }
        }
      }
      &-text {
        &__input {
          color: #999999;
          border-width: 0 0 1px 0;
          border-radius: 0;
          font-weight: bold;

          &:not([readonly]) {
            &:focus {
              color: #333333;
            }
          }
        }
      }
      &__cmt {
        padding-top: 2rem;
        font-size: 1.2rem;
        color: #999999;
        letter-spacing: -0.05em;
      }
      > .btn-login {
        &[disabled] {
          color: #cfcfcf;
          border-color: #efefef;
          background: #efefef;
        }
      }
    }
  }

  /*
  ■■■■■■■■■■■■■■■■■■■■■■■■■■■■■■■■■■■■■■■■■■■■■■■■■■■■■■■■■■■■■■■■■■■■■■■■■■■■■■■■■■■■■■■■■■■■■■■■■■
  */

  /*  로그인 - 2단계 인증 */
  .login--auth {
    width: 36rem;
    margin: 0 auto;
    .form {
      padding-bottom: 3.6rem;
      &-row {
        &[data-row="phone"] {
          display: flex;
          align-items: center;
          margin-bottom: 3.4rem;

          input {
            flex: 1;
            margin-right: 1rem;

            &.form-text__input {
              width: calc(100% - 3rem);
            }
          }
          button {
            // width: 7.3rem;
            padding: 0 1.2rem;
            font-weight: 300;
          }
        }
        &[data-row="certification"] {
          position: relative;
          margin-bottom: 6.2rem;

          &.success {
            &:before {
              content: "";
              position: absolute;
              top: 50%;
              left: 7.5rem;
              color: #1abaa7;
              width: 1.2rem;
              height: 0.9rem;
              margin-top: -0.45rem;
              line-height: 1.5;
              pointer-events: none;
              border: 0;
              background-image: svgIcon(check, 1ABAA7), svgIcon(circle, fff);
              background-position: center center;
              background-repeat: no-repeat;
              background-size: 1.08333em;
            }
          }

          .form-text__watch {
            position: absolute;
            left: 0;
            bottom: -3rem;
            color: #666666;
            font-size: 1.2rem;

            button {
              font-weight: 700;
            }
          }
        }

        > .form-text__label {
          position: absolute;
          left: 0;
          bottom: -2.6rem;
          font-size: 1.2rem;
          color: #f86666;
        }
      }

      &__cmt {
        position: absolute;
        bottom: 0;
        left: 50%;
        width: 40rem;
        margin-left: -20rem;
        text-align: center;
      }
    }
  }

  /*
  ■■■■■■■■■■■■■■■■■■■■■■■■■■■■■■■■■■■■■■■■■■■■■■■■■■■■■■■■■■■■■■■■■■■■■■■■■■■■■■■■■■■■■■■■■■■■■■■■■■
  */
}
