// UI - Date Picker
.ui_calendar {
  position: relative;

  > .date_wrap {
    padding: 1.2rem 2rem;
    height: 6rem;
    background: #fafafa;
    text-align: center;

    .result {
      padding: 0 0 0.5rem 0;
      color: $font_color_hl;
      font-size: 1.6rem;
      font-weight: bold;
      line-height: 1;
      text-align: center;
      white-space: nowrap;
    }

    .date {
      color: $point_color;
      font-size: 1.4rem;
      line-height: 1;
      white-space: nowrap;
    }
  }

  > .calendar_wrap {
    background: #ffffff;
  }

  > .time_wrap {
    position: relative;
    padding: 5px 5px 5px 49px;
    margin: 0 10px 10px;
    background: #ededed;
    line-height: 24px;

    > strong {
      position: absolute;
      top: 5px;
      left: 0;
      width: 49px;
      text-align: center;
    }

    .wrap {
      @include clearBoth();

      .dcp {
        width: 83px;
        text-align: center;

        &:first-child {
          float: left;
        }

        &:last-child {
          float: right;
        }
      }
    }
  }

  > .btns_grp {
    position: absolute;
    top: 6rem;
    left: 0;
    width: 100%;
    height: 3rem;
    border-top: 1px solid #e5e5e5;
    border-bottom: 1px solid #e5e5e5;
    box-sizing: border-box;

    > button {
      position: absolute;
      top: 0;
      width: 3rem;
      height: 3rem;
      margin: -1px 0;
      border: 1px solid #e5e5e5;
      box-sizing: border-box;
      @include transition();

      .icon {
        display: none;
      }

      &:before {
        content: "";
        display: block;
        color: #a9aaac;
        font-family: "icomoon";
        // line-height: 28px;
        text-align: center;
        @include transition();
      }

      &:hover {
        background: $component_bg_over_color;
        border: 1px solid $component_brd_color;

        &.btn_prv_y {
          & + .btn_prv_m {
            border-left-color: $component_brd_color;
          }
        }

        &.btn_nxt_m {
          & + .btn_nxt_y {
            border-left-color: $component_brd_color;
          }
        }

        &:before {
          // color: $component_brd_color;
        }
      }
    }

    .btn_prv_y {
      left: 0;
      border-left: 1px solid #ffffff;
      border-right: 1px solid #e9e9e9;

      &:before {
        content: "\e003";
      }
    }

    .btn_prv_m {
      left: 2.9rem;
      border-right: 1px solid #e9e9e9;

      &:before {
        content: "\e001";
      }
    }

    .btn_nxt_m {
      right: 2.9rem;
      border-left: 1px solid #e9e9e9;

      &:before {
        content: "\e000";
      }
    }

    .btn_nxt_y {
      right: 0;
      border-left: 1px solid #e9e9e9;
      border-right: 1px solid #ffffff;

      &:before {
        content: "\e002";
      }
    }
  }
}

.ui-datepicker {
  display: none;
  overflow: hidden;
  background: #fff;
  width: auto !important;

  .ui-datepicker-header {
    position: relative;
    height: 3rem;
    text-align: center;

    .ui-datepicker-title {
      @include inlineBlock();
      text-align: center;
      line-height: 1;
      @include clearBoth();
    }

    .ui-datepicker-prev {
      display: none;
      position: absolute;
      top: 50%;
      left: 3px;
      width: 15px;
      height: 14px;
      margin: -7px 0 0 0;
      background: url(#{$ui_img_path}ui_datepicker_arrow.png) no-repeat 4px 4px;
      text-indent: -9999px;
      cursor: pointer;
    }

    .ui-datepicker-next {
      display: none;
      position: absolute;
      top: 50%;
      right: 3px;
      width: 15px;
      height: 14px;
      margin: -7px 0 0 0;
      background: url(#{$ui_img_path}ui_datepicker_arrow.png) no-repeat -12px 4px;
      text-indent: -9999px;
      cursor: pointer;
    }

    .ui-datepicker-month {
      float: right;
    }

    .ui-datepicker-year {
      float: right;

      &:after {
        content: "-";
      }
    }
  }

  .ui-datepicker-calendar {
    width: 209px;
    margin: 20px 14px 18px;
    overflow: hidden;
    table-layout: fixed !important;
    border-spacing: 0 3px;

    thead {
      th {
        padding: 0 0 6px 0;
        color: #c0c0c0;
        font-weight: normal;
        text-align: center !important;

        > * {
          &[title="Sunday"] {
            // color: $point_color2;
          }
        }
      }
    }

    tbody {
      tr {
        display: block;
        margin-bottom: 2px;
        td {
          padding: 0;
          border-top: 1px solid transparent;
          border-bottom: 1px solid transparent;
          text-align: center;
          font-size: 1em;
          line-height: 1;

          &.is-holiday {
            // color: $point_color2;
          }

          &.ui-datepicker-week-col {
            width: 28px;
            height: 28px;
            color: $point_color;

            > * {
              // width: 24px;
              // height: 24px;
              border: none;
              border-radius: 50%;
              // line-height: 24px;
            }

            &.is-range {
              > * {
                border-radius: 50%;
                background: $point_color;
                color: #ffffff;
              }
            }
          }

          > *,
          .ui-state-default {
            display: block;
            width: 28px;
            height: 28px;
            margin: 0;
            border: 1px solid transparent;
            border-radius: 50%;
            box-sizing: border-box;
            line-height: 22px;
            text-align: center;
            // @include transition();

            &.ui-state-hover {
              border: 1px solid #ebebeb;
              background: $component_bg_over_color;
              color: #505050 !important;
            }

            &:link,
            &:visited {
              color: #999999;
            }

            &.ui-state-active {
              border-color: #424242;
              background: #424242;
              color: #ffffff !important;
            }

            &.ui-priority-secondary {
              color: #909090 !important;
              opacity: 0.5;
            }
          }

          &.ui-state-disabled,
          &.ui-datepicker-other-month {
            color: #cdd0da;
          }

          &.is-range {
            background: $component_bg_over_color;

            &:first-child {
              border-radius: 100px 0 0 100px;
            }

            &:last-child {
              border-radius: 0 100px 100px 0;
            }

            > * {
              // border-radius: 0;
              // color: #ffffff !important;
            }

            // & + .is-range {
            //     a {
            //         border-radius: 0;
            //     }
            // }

            &.is-range-first {
              border-radius: 100px 0 0 100px;

              > * {
                // border-color: $brd_color_hl;
                // border-radius: 50%;
                // border-top-left-radius: 4px;
                // border-bottom-left-radius: 4px;
                // background: $brd_color_hl;
                // color: #ffffff !important;
              }
            }

            &.is-range-last {
              border-radius: 0 100px 100px 0;

              > * {
                // border-color: $brd_color_hl;
                // border-top-right-radius: 4px;
                // border-bottom-right-radius: 4px;
                // background: $brd_color_hl;
                // color: #ffffff !important;
              }
            }
          }
        }
      }
    }

    @at-root .is-week & {
      width: 224px;
    }
  }
}

.ui_datepickers {
  @include inlineBlock();
  position: relative;
  font-size: 1em;
  white-space: nowrap;
  vertical-align: top;

  > .grps {
    @include inlineBlock();
    padding: 0 10px !important;
    margin-left: 10px;
    background: #ffffff;
    border-radius: 100px;
    border: 1px solid #ededef;
    vertical-align: middle;
    @include transition();

    > ul {
      @include clearBoth();

      > * {
        float: left;
        margin: 0 7px 0 0;

        &:last-child {
          margin: 0;
        }

        .dcp {
          min-width: 40px;
        }
      }
    }

    &:hover {
      // background: $component_bg_over_color;
    }

    .dcp {
      margin-right: 7px !important;

      input {
        & + label {
          border: 1px solid transparent !important;
          border-bottom: 1.5px solid transparent !important;
          border-radius: 0 !important;
          padding: 0 2px !important;
          min-width: 30px;
          text-align: center;
          background: #ffffff;
          line-height: 1.8em;
        }

        &:hover {
          & + label {
            background: transparent;
            border-bottom: 1.5px solid #000000 !important;
          }
        }

        &:checked {
          & + label {
            background: #424242;
            border: 1px solid #424242 !important;
            border-radius: 100px !important;
          }
        }
      }

      &:last-child {
        margin-right: 0 !important;
      }
    }
  }

  > .inputs {
    @include inlineBlock();
    position: relative;
    padding: 0 0 0 75px;
    line-height: 1em;
    vertical-align: middle;
    @include transition();

    > .cal_type {
      position: absolute;
      top: 50%;
      left: 0;
      width: 65px;
      margin-top: -0.9em;
      box-sizing: border-box;
      font-size: 1em;

      > .btn_type {
        width: 100%;
        padding: 0 10px;
        border: 1px solid #ededef;
        border-radius: 100px;
        background: #ffffff;
        box-sizing: border-box;
        color: #999999;
        line-height: 1.8em;
        overflow: hidden;
        text-align: left;
        overflow: hidden;
        vertical-align: top;
        @include transition();

        &:before {
          content: "\e010";
          position: absolute;
          top: 0;
          right: 1px;
          display: block;
          width: 28px;
          height: 100%;
          padding-top: 1px;
          // line-height: 1.4em;
          font-family: "icomoon";
          font-size: 15px;
          text-align: center;
          vertical-align: middle;
          @include opacity(0);
          @include transition();
        }

        &:after {
          content: "\e009";
          position: absolute;
          top: 0;
          right: 1px;
          display: block;
          width: 28px;
          height: 100%;
          padding-top: 1px;
          // line-height: 1.4em;
          font-family: "icomoon";
          font-size: 15px;
          text-align: center;
          vertical-align: middle;
          @include transition();
        }
      }
      > .list {
        position: fixed;
        width: 67px;
        margin: -6px 0 0 0;
        // border: 1px solid $component_brd_color;
        border-radius: 10px;
        background: #ffffff;
        box-sizing: border-box;
        overflow: hidden;
        visibility: hidden;
        z-index: 1;
        @include opacity(0);
        @include transition(0.12, 0, (margin, opacity));
        @include shadow(0, 1px, 6px, 0.05, 0, 0, 0);

        button {
          display: block;
          width: 100%;
          color: #999999;
          padding: 0 1rem;
          box-sizing: border-box;
          text-align: left;
          line-height: 2.4rem;
          @include transition();

          &:hover {
            background: #ededef;
            color: #333333;
          }
          &.is-active {
            background: #212121;
            color: #ffffff;
          }
        }
      }

      &.is-active {
        z-index: 2;

        > .btn_type {
          &:before {
            margin: 0;
            @include opacity(1);
          }
          &:after {
            margin: 8px 0 0 0;
            @include opacity(0);
          }
        }
        > .list {
          // top: 32px;
          margin: 2px 0 0 0;
          visibility: visible;
          @include opacity(1);
        }
      }

      // &:hover {
      //     border-color: $component_brd_over_color;
      //     background: $component_bg_over_color;
      //     // z-index: 2;
      // }
    }

    > .result {
      display: block;
      position: relative;
      // width: 100%;
      height: 3rem;
      padding: 0 15px;
      border: 1px solid #ededef;
      border-radius: 100px;
      margin-left: -1px;
      background: #ffffff;
      color: #999999;
      font-size: 1em;
      @include transition();

      &:before {
        content: "\e069";
        @include inlineBlock();
        padding: 0 4px 0 0;
        font-family: "icomoon";
        color: #a9aaac;
        font-size: 1.4em;
        vertical-align: top;
        @include transition();
      }

      &:hover {
        background: #ededef;
      }

      &.is-active {
        color: #333333;
        z-index: 2;

        &:before {
          color: #333333;
        }
      }

      &:first-child {
        border-radius: 8px;
        margin-left: 0;
      }
    }

    &.is-move {
      padding-left: 94px !important;
      padding-right: 28px !important;

      > .cal_type {
        border-radius: 0;
        left: 29px;
      }

      > .result {
        border-radius: 0;
      }

      > .btn_prev,
      > .btn_next {
        position: absolute;
        top: 0;
        width: 30px;
        // border: 1px solid $component_brd_color;
        // background: $component_bg_color;
        font-family: "icomoon";
        text-align: center;

        &:hover {
          // border-color: $component_brd_over_color;
          // background: $component_bg_over_color;
          z-index: 2;
        }
      }

      > .btn_prev {
        left: 0;
        border-radius: 8px 0 0 8px;
      }
      > .btn_next {
        right: 0;
        border-radius: 0 8px 8px 0;
      }
    }

    // &:hover {
    //     border: 1px solid $component_brd_active_color;
    //     background: $component_bg_active_color;
    // }
  }

  > .calendars {
    display: table;
    position: fixed;
    // top: 32px;
    // min-width: 100%;
    margin-top: -6px;
    border: 1px solid $component_brd_color;
    // padding-bottom: 25px;
    background: #ffffff;
    visibility: hidden;
    z-index: 2;
    overflow: hidden;
    @include opacity(0);
    @include transition(0.12, 0, (opacity, margin, visibility));
    @include shadow(0, 1px, 3px, 0.1, 0, 0, 0);

    > .dp {
      display: table-cell;

      &:first-of-type:not(:last-of-type) {
        padding-right: 1px;

        &:after {
          content: "";
          position: absolute;
          top: 0;
          left: calc(50% - 1px);
          width: 1px;
          height: 100%;
          background: $component_brd_color;
          z-index: 1;
        }
      }

      .calendar_wrap {
        padding: 1.5rem 1rem;
        white-space: nowrap;

        > .dcp {
          width: 10rem;
          height: 2.5rem;

          &:not(:first-child) {
            margin-left: 4px;
          }

          > select {
            height: 100%;

            & + label {
              border: 1px solid #dddddd;
              border-radius: 100px;
              padding: 0 1.5rem 0 1rem;
              line-height: 2.3rem;
              font-size: 1em;
              text-overflow: unset;

              &:before {
                content: "\e010";
                height: 2.3rem;
                line-height: 2.3rem;
              }
              &:after {
                content: "\e009";
                height: 2.3rem;
                line-height: 2.3rem;
              }
            }

            &:focus {
              & + label {
                background: #212121;
                color: #ffffff;

                &:after,
                &:before {
                  color: #ffffff;
                }
              }
            }
          }
        }
      }

      &:first-of-type:not(:only-of-type) {
        td {
          &.is-range {
            &.is-range-last {
              > * {
                // background: transparent;
                // border-color: $component_brd_active_color;
                // border-radius: 0;
                // color: #ffffff !important;
              }
            }
          }
        }
      }
      &:last-of-type:not(:only-of-type) {
        td {
          &.is-range {
            &.is-range-first {
              > * {
                // background: $component_brd_active_color;
                // border-color: $component_brd_active_color;
                // border-radius: 0;
                // color: #ffffff !important;
              }
            }
          }
        }
      }
    }

    > .cal_info_wrap {
      position: absolute;
      bottom: 0;
      left: 0;
      display: block;
      width: 100%;
      background: #fafafa;
      color: #999999;
      border-top: 1px solid #e5e5e5;
      padding: 0 9px;
      font-size: 11px;
      line-height: 24px;
      text-align: left;
      box-sizing: border-box;
      z-index: 3;
    }

    &.is-active {
      margin-top: 2px;
      visibility: visible;
      @include opacity(1);
    }
  }

  // 일별 달력 일 때
  &.is-day {
    > .calendars {
      min-width: auto;
    }

    .calendar_wrap {
      padding: 0 !important;
    }
  }

  // 주별 달력 일 때
  &.is-week {
    .calendar_wrap {
      padding: 0 !important;

      .ui-datepicker {
        .ui-datepicker-calendar {
          thead {
            tr {
              th {
                &:first-child {
                  color: $point_color;
                }
              }
            }
          }
          tbody {
            tr {
              td {
                color: #909090;

                &.is-range {
                  &:first-child {
                    background: none;
                    border-color: transparent;
                  }

                  &:nth-child(2) {
                    border-radius: 100px 0 0 100px;
                  }
                }
              }
            }
          }

          @at-root .is-week & {
            width: 224px;
          }
        }
      }
    }
  }

  // 월별 달력 일 때
  &.is-month {
    .calendar_wrap {
      > .dcp {
        width: 8rem !important;
      }
    }
  }

  // 활성화
  &.is-active {
    > .inputs {
      // border: 1px solid $component_active_color;
      // background: $component_bg_over_color;
    }
  }

  // 타입 선택 없을 때
  &.is-not-type {
    > .inputs {
      padding: 0 !important;

      > .cal_type {
        display: none;
      }

      > .result {
        padding: 0;
      }

      &.is-move {
        padding-left: 30px !important;
        padding-right: 28px !important;

        > .result {
          border-radius: 0;
          padding: 0 20px;
        }
      }
    }
  }

  // Small
  &.is-small {
    > .inputs {
      line-height: 22px;

      > .cal_type {
        > .btn_type {
          line-height: 22px;
        }
        > .list {
          button {
            padding: 0 6px;
          }
        }
      }

      > .result {
        height: 24px;

        &:after {
          padding: 0 0 0 10px;
        }
      }

      &.is-move {
        padding-left: 81px !important;
        padding-right: 22px !important;

        .cal_type {
          left: 23px;
        }

        .btn_prev,
        .btn_next {
          width: 24px;
        }
      }

      // &:hover {
      //     border: 1px solid $component_brd_active_color;
      //     background: $component_bg_active_color;
      // }
    }

    &.is-not-type {
      > .inputs {
        &.is-move {
          padding-left: 24px !important;
          padding-right: 22px !important;
        }
      }
    }

    & + .ui_btn {
      margin-top: -2px;
      margin-left: 6px;
    }
  }
}

.datepickers {
  > strong {
    & + .ui_datepickers {
      margin-left: 3rem;
    }
  }
}
