*/

/* inline icons */
@mixin iconSvgOption($name, $color: #000) {
  display: inline-block;
  width: 1em;
  height: 1em;
  font-size: var(--font-size, $G-font-size);
  &::after {
    content: "";
    display: inline-block;
    width: 1em;
    height: 1em;
    background-color: var(--icon-svg-color, $color);
    mask-image: svgIcon($name);
    mask-repeat: no-repeat;
    mask-position: center;
    mask-size: contain;
    -webkit-mask-image: svgIcon($name);
    -webkit-mask-repeat: no-repeat;
    -webkit-mask-position: center;
    -webkit-mask-size: contain;
  }
}

.icon-svg {
  $names: close, back, circle, check, calendar, classify, memo, chart-word-cloud, normal-board, triangle-down, triangle-up, search, interj, bar, play, arrow-up,
    bar, user, lock, eye, eye-slash, thumbs-up, fire, chevron-up, chevron-down, chevron-left, chevrons-left, chevron-right, chevrons-right, twitter, facebook,
    emotion-positive, emotion-neutral, emotion-negative, noty-info, noty-success, noty-error;

  @each $name in $names {
    &#{"-" + $name} {
      @include iconSvgOption($name);
    }
  }
}
