@charset "utf-8";

#sidebar {
  position: fixed;
  top: 23.1rem;
  left: 0;
  z-index: 21;
  width: 100%;
  .inner {
    .side-wrap {
      position: absolute;
      right: -20.8rem;
      width: 16.2rem;
      background: $G-default-background-color;
      overflow: hidden;
      box-sizing: border-box;
      .side-box {
        border: 1px solid #ededef;

        letter-spacing: -0.03em !important;
        .side-box-title {
          // padding: 10px 15px;
          padding: 1rem 1.5rem;
          font-size: 1.2rem;
          color: #333;
          text-align: center;
          border-bottom: 1px solid #ededef;
        }
        ul {
          margin-top: 0.2rem;
          margin-bottom: 0.9rem;
          li {
            > button {
              line-height: 1.9rem !important;
              position: relative;
              display: block;
              padding: 0.6rem 0 0 1.7rem;
              color: $component_font;
              font-size: 1.2rem;
              line-height: 1.6rem;
              font-weight: normal;
              word-break: keep-all;
              text-align: left;
              @include transition();

              &:before {
                content: "";
                position: absolute;
                top: 1rem;
                left: 0.8rem;
                width: 0.3rem;
                height: 1.1rem;
                // background: $point_color;
                border-radius: 2px;
                opacity: 0;
                @include transition();
              }
            }

            &:hover {
              > button {
                color: $G-color-default;
                font-weight: bold;

                &:before {
                  background: $G-color-secondary;
                  opacity: 1;
                }
              }
            }
            .active {
              color: #333333;
              font-weight: bold;

              &:before {
                background: $G-color-secondary;
                opacity: 1;
              }
            }
          }
        }
      }
    }

    .side-topBtn {
      margin-top: 1.1rem;
      width: 100%;
      height: 3.1rem;
      background: $G-default-background-color;
      border: 1px solid #ededef;
      text-align: center;
      box-sizing: border-box;
      @include transition();

      > span {
        display: inline-block;
        color: $component_font;
        padding-left: 0.2rem;
        font-size: 1.2rem;
        font-weight: bold;
        line-height: 3rem;
        text-align: center;
        vertical-align: top;
        @include transition();
      }

      i:after {
        margin-top: 1rem;
        background-color: $G-default-color;
        font-size: 1.2rem;
        width: 1rem !important;
        height: 1rem !important;
      }
      &:hover {
        background: $component_back;

        &:before {
          color: $point_color;
        }
      }
    }
  }
}
