/* SCSS Document */
@charset "UTF-8";

/****************************************************************************
  badge - 라벨 컴포넌트
****************************************************************************/

.c-badge {
  $colorSet: --color-set;
  $bgcolor: --bgcolor;
  $color: --color;
  $fontSize: 1.2rem;

  display: inline-flex;
  align-items: center;
  height: fz-in-em(24, $fontSize);
  padding: 0 fz-in-em(10, $fontSize);
  border-radius: 0.2rem;
  font-size: $fontSize;
  color: var($color, #fff);
  background-color: var($colorSet, var($bgcolor, $G-disable-background-color));
  &--checked {
    background-color: $G-checked-background-color;
  }
  &--regular {
    color: $G-color-default;
    border: 1px solid $G-color-border;
    background-color: #fff;
  }
}

/****************************************************************************
  container 전체 검색바
****************************************************************************/
.c-search-bar {
  position: relative;
  z-index: 10;
  height: 6rem;
  background-color: #e5e5e5;
  [data-header-fixed="true"] & {
    position: sticky;
    top: $G-header-height;
  }
  .inner {
    display: flex;
    align-items: center;
  }
  .option {
    flex: none;
    .datepickers {
      display: flex;
      align-items: center;
    }
  }
  .btn-search {
    width: 12rem;
    margin-left: auto;
  }
}

/****************************************************************************
  페이징 - 기본
****************************************************************************/
.c-pagination {
  $c: c-pagination;
  $it: c-pagination__item;
  $bt: c-pagination__btn;

  display: flex;
  justify-content: center;
  margin: 0 auto;
  &__item {
    .#{$c}__btn {
      display: flex;
      justify-content: center;
      align-items: center;
      width: 3rem;
      height: 3rem;
      font-size: 1.2rem;
      color: $G-default-color;
      @include transition;
      &--is-active {
        color: #fff;
        background-color: $G-checked-background-color;
      }
      &:not(.#{$bt}--is-active):hover {
        color: #424242;
      }
      @at-root .#{$c} .#{$it} {
        &--first,
        &--last,
        &--prev,
        &--next {
          .#{$c}__btn {
            border: 1px solid $G-color-border;
            [class*="icon"] {
              --icon-svg-color: #a9aaac;
              font-size: 1rem !important;
              margin : 0 !important;
              padding : 0 !important;
              &::after {
                @include transition;
              }
            }
            &:hover {
              [class*="icon"] {
                --icon-svg-color: #424242;
              }
            }
            &[disabled] {
              [class*="icon"] {
                --icon-svg-color: #dedede;
              }
            }
          }
        }
        &--first .#{$c}__btn {
          border-right: 0;
        }
        &--last .#{$c}__btn {
          border-left: 0;
        }
      }
    }
  }
}

/****************************************************************************
  data-table, total
****************************************************************************/
.c-data-table-total {
  font-size: 1.2rem;
  color: #999;
  .count {
    color: $G-color-default;
  }
}
