/* SCSS Document */
@charset "UTF-8";

/****************************************************************************
    cache
****************************************************************************/
$G-cache: random(999999999);

/****************************************************************************
    Breakpoints                               https://getbootstrap.com 참고함
****************************************************************************/
$G-breakpoint-small: 576px;
$G-breakpoint-medium: 768px;
$G-breakpoint-large: 992px;
$G-breakpoint-extraLarge: 1200px;
$G-breakpoint-extraExtraLarge: 1400px;
$G-breakpoint-fhd: 1920px;
$G-breakpoint-qhd: 2048px;
$G-breakpoint-thd: 2880px;
$G-breakpoint-uhd: 3600px;

/****************************************************************************
    sizing
****************************************************************************/
$G-header-height: 9.5rem;
$G-inner-width: 140rem;

/****************************************************************************
    radius
****************************************************************************/
$G-box-radius: 2rem;

/****************************************************************************
    font
****************************************************************************/
$G-font-family: "맑은 고딕", "Malgun Gothic", "돋움", "Dotum", "AppleSDGothicNeo-Light", "Sans-serif", "Tahoma";
$G-font-size: 1.4rem;
$G-letter-spacing: -0.02em;

/****************************************************************************
    color
****************************************************************************/
/* coloring */
$G-color-default: #333;
$G-color-primary: #0b396a;
$G-color-secondary: #a6937c;
$G-color-hover: #269ff6;
$G-color-active: #6f00e4;
$G-color-border: #e5e5e5;

/* state color */
$G-default-border-color: $G-color-border;
$G-default-color: #999;
$G-default-background-color: #fff;
$G-hover-background-color: #f6f6f6;
$G-focus-border-color: #424242;
$G-focus-background-color: #ededef;
$G-checked-border-color: $G-color-border;
$G-checked-color: #fff;
$G-checked-background-color: #424242;
$G-disable-border-color: $G-color-border;
$G-disable-color: #c3c3c3;
$G-disable-background-color: #dedede;

/****************************************************************************
    linear-gradient
****************************************************************************/
$G-grad-primary: linear-gradient(90deg, #4b98f2 0%, #604aff 100%);
$G-grad-hover: linear-gradient(90deg, #4b98f2 0%, #4b98f2 100%);
$G-grad-active: linear-gradient(90deg, #604aff 0%, #4b98f2 100%);

/****************************************************************************
    shadow
****************************************************************************/
$G-box-shadow: 0 0 3rem rgba(7, 7, 7, 0.12);
$G-button-shadow: 0 0.3rem 1.7rem rgba(46, 43, 176, 0.4);
$G-text-shadow: 0 0.05rem 0.2rem rgba(46, 43, 176, 0.6);

/****************************************************************************
    animate
****************************************************************************/
$G-container-delay: 0.25;

/****************************************************************************
    path
****************************************************************************/
$G-img-path: "/dashboard/assets/img";
