/* SCSS Document */
@charset "UTF-8";

/****************************************************************************
  section - default style
****************************************************************************/
.l-section {
  padding: 4rem 0 0;
  &-header {
    display: flex;
    flex-flow: row wrap;
    align-items: center;
    padding-bottom: 0.8rem;
    border-bottom: 1px solid $G-color-border;
    &__title {
      position: relative;
      padding-left: 1.5rem;
      font-size: 2.1rem;
      font-weight: 700;
      &::before {
        content: "";
        position: absolute;
        top: 50%;
        left: 0;
        width: 0.6rem;
        height: 0.6rem;
        background-color: $G-color-default;
        transform: translateY(-50%);
      }
    }
  }
  &-body {
    padding-top: 1.5rem;
  }
}

/****************************************************************************
  card - 콘텐츠 레이아웃
****************************************************************************/
.l-card {
  display: flex;
  flex-direction: column;
  padding: 2rem;
  border: 1px solid $G-color-border;
  &-header {
    display: flex;
    flex-flow: row wrap;
    align-items: center;
    width: 100%;
    &__title {
      font-size: 1.5rem;
      font-weight: 700;
      .c-badge:first-child {
        margin-left: 1em;
      }
    }
  }
  &-body {
    position: relative;
    height: 100%;
  }
  @at-root .col {
    > .l-card {
      height: 100%;
    }
  }
}
