@charset "utf-8";

#notys {
  
  > div {
    position: fixed;
    z-index: 99999;
    left: 0;
    right: 0;
    padding: 1rem;
  }
  pointer-events: none;
  // opacity: 0.5;
  // transform: translateY(100%);
  // width: 100%;
  // height: 200px;
  // border: 5px solid red;
  ul{
    // border: 3px solid black;
    // padding: 10px;
    li{
      position: relative;
      width: 26rem;
      min-height: 3rem;
      max-height: 6rem;
      border-radius: 3px;
      margin-bottom: 0.3rem;
      padding-left: 1.1rem;
      padding-right: 1.1rem;
      i {
        margin-right: 0.9rem;
        position: absolute;
        top: 0.7rem;
        left: 1rem;
        &::after {
          background-color: #ededef !important;
          width: 1.6rem !important;
          height: 1.6rem !important;
        }
      }
      .notys-msg {
        padding-left: 2rem;
        line-height: 2.8rem;
        font-size: 1.2rem;
        color: #fff !important;
        font-weight: bold;
      }
    }
  }
}


/* noty영역 y에 대한 좌표 설정 */
.notys--is-y-top {
  top: 0;
}

.notys--is-y-center {
  top: 50%;
  transform: translateY(-50%) !important;
}

.notys--is-y-bottom {
  bottom: 0;
}

/* noty영역 x에 대한 좌표 설정 */
.notys--is-x-left {
  left: 0;
}

.notys--is-x-center {
  left: 50%;
  transform: translateX(-50%) !important;
}

.notys--is-x-right {
  left: 100%;
  transform: translateX(-100%) !important;
}


/* noty유형에 따른 배경색,로고 지정 */
.notys-info {
  background-color: $G-checked-background-color;
}

.notys-success {
  background-color: $G-color-primary;
}

.notys-error {
  background-color: #831037;
}