/* SCSS Document */
@charset "UTF-8";

/****************************************************************************
    header
****************************************************************************/
/* header */
#header {
  overflow: hidden;
  background-color: #fff;
}

/*
■■■■■■■■■■■■■■■■■■■■■■■■■■■■■■■■■■■■■■■■■■■■■■■■■■■■■■■■■■■■■■■■■■■■■■■■■■■■■■■■■■■■■■■■■■■■■■■■■■
*/

/* top menu */
#header {
  .header-top {
    display: flex;
    align-items: center;
    justify-content: space-between;
    height: 4rem;
    &__logo {
      .link {
        display: block;
      }
      .logo {
        width: auto;
        height: 2.7rem;
      }
    }
    &__list {
      display: flex;
      margin-right: -6rem;
    }
    &__item {
      padding: 0 0.25rem;
      &--user {
        display: inline-flex;
        align-items: center;
        font-size: 1.2rem;
      }
      &.header_fix_pin {
        padding-left: 3rem;
        .ui_btn {
          [data-header-fixed="true"] & {
            color: #fff;
            background-color: #424242;
          }
        }
      }
      .ui_btn {
        display: inline-flex;
        justify-content: center;
        align-items: center;
        width: 3rem;
        height: 3rem;
        padding: 0;
        border-radius: 100%;
      }
    }
  }
}

/*
■■■■■■■■■■■■■■■■■■■■■■■■■■■■■■■■■■■■■■■■■■■■■■■■■■■■■■■■■■■■■■■■■■■■■■■■■■■■■■■■■■■■■■■■■■■■■■■■■■
*/

/* GNB */
#header {
  .header-gnb {
    display: flex;
    justify-content: space-around;
    position: relative;
    width: 100%;
    height: 5.5rem;
    &::after {
      content: "";
      position: absolute;
      top: 0;
      right: 0;
      width: 300%;
      height: 100%;
      background-color: $G-color-primary;
    }
    &::before {
      content: "";
      position: absolute;
      top: 0;
      left: 100%;
      width: 300%;
      height: 100%;
      background-color: $G-color-secondary;
    }
    &__link {
      display: inline-flex;
      justify-content: center;
      align-items: center;
      flex: 1;
      position: relative;
      z-index: 1;
      font-size: 1.8rem;
      color: #fff;
      &:last-of-type {
        color: $G-color-primary;
        background-color: $G-color-secondary;
      }
      &:hover,
      &--is-active {
        color: #83e0fe;
      }
    }
  }
}

