/* SCSS Document */
@charset "UTF-8";

/****************************************************************************
    modal - modal 팝업 요소
****************************************************************************/
@mixin modalFadeIn($translate: top) {
  @if ($translate == "top") {
    $translate: translate(-50%, -10%);
  } @else if ($translate == "bottom") {
    $translate: translate(-50%, -90%);
  } @else if ($translate == "left") {
    $translate: translate(90%, -50%);
  } @else if ($translate == "right") {
    $translate: translate(-190%, -50%);
  }

  &.modal--is-active {
    .modal-wrapper {
      opacity: 1;
      transform: translate(-50%, -50%);
      @include transition($delay: 0.2, $tgs: opacity transform);
    }
    .modal-dimmed {
      background-color: rgba(0, 0, 0, 0.7);
      @include transition($tgs: background-color);
    }
  }
  &:not(.modal--is-active) {
    .modal-wrapper {
      opacity: 0;
      transform: $translate;
    }
    .modal-dimmed {
      background-color: rgba(0, 0, 0, 0);
    }
  }
}

@mixin modalFadeOut($transform: bottom) {
  @if ($transform == "top") {
    $transform: translate(-50%, -90%);
  } @else if ($transform == "bottom") {
    $transform: translate(-50%, -10%);
  } @else if ($transform == "left") {
    $transform: translate(-90%, -50%);
  } @else if ($transform == "right") {
    $transform: translate(10%, -50%);
  }

  &.modal--is-disabled {
    .modal-wrapper {
      opacity: 0;
      transform: $transform;
      @include transition($time: 0.3, $tgs: opacity transform);
    }
    .modal-dimmed {
      background-color: rgba(0, 0, 0, 0);
      @include transition($time: 0.3, $delay: 0.3, $tgs: background-color);
    }
  }
}

/* 공통 */
.modal {
  position: fixed;
  z-index: 100;
  top: 0;
  left: 0;
  width: 100vw;
  height: 100%;
  font-family: $G-font-family;
  font-size: $G-font-size;
  transform: translate(0, 0);
  pointer-events: none;
  &--is-active {
    pointer-events: all;
  }
}
.modal-wrapper {
  overflow: auto;
  position: absolute;
  z-index: 101;
  top: 50%;
  left: 50%;
  background-color: #fff;
  transform: translate(-50%, -50%);
  &:empty {
    width: 50vw;
    height: 50vw;
    border-radius: 0.5rem;
  }
}
.modal-header {
  position: relative;
  &__icon-close {
    position: absolute;
    z-index: 101;
    top: 2.1rem;
    right: 2.1rem;
    font-size: 2.4rem;
    @include transition;
    &:after {
      @include svgIconOption(close, 999);
      background-size: 70%;
    }
  }
}
.modal-footer {
  position: relative;
}
.modal-dimmed {
  position: absolute;
  top: 0;
  bottom: 0;
  left: 0;
  right: 0;
  width: 100%;
  height: 100%;
  background-color: rgba(0, 0, 0, 0.7);
  touch-action: none;
}

/*
■■■■■■■■■■■■■■■■■■■■■■■■■■■■■■■■■■■■■■■■■■■■■■■■■■■■■■■■■■■■■■■■■■■■■■■■■■■■■■■■■■■■■■■■■■■■■■■■■■
*/

/* 경고 및 확인창 - 공통 */
.modal-alert,
.modal-confirm {
  @include modalFadeIn;
  @include modalFadeOut;

  .modal-wrapper {
    min-width: 20rem;
    max-width: 50rem;
    padding: 3rem;
    border-radius: 1rem;
  }
  .modal-header {
    &__title {
      display: block;
      margin-bottom: 3rem;
      word-break: keep-all;
      line-height: 1.8em;
      strong {
        font-weight: 500;
        white-space: break-spaces;
      }
    }
    &__icon-close {
      display: none;
    }
  }
  .modal-footer {
    &__btn-area {
      display: flex;
      justify-content: center;
      margin: 0 -0.4rem;
    }
    .btn {
      margin: 0 0.4rem;
    }
    .btn-cancel .txt:empty::before {
      content: "취소";
    }
    .btn-agree .txt:empty:before {
      content: "확인";
    }
  }
}

/* 확인창 */
.modal-confirm {
  .modal-footer {
    .btn-cancel {
      border: 1px solid $G-color-border;
      color: $G-default-color;
      background-color: #fff;
    }
  }
}

/*
■■■■■■■■■■■■■■■■■■■■■■■■■■■■■■■■■■■■■■■■■■■■■■■■■■■■■■■■■■■■■■■■■■■■■■■■■■■■■■■■■■■■■■■■■■■■■■■■■■
*/

// data-table public style
.modal-data-table {
  @include modalFadeIn;
  @include modalFadeOut;

  .modal-wrapper {
    width: 120rem;
    border-radius: 1.5rem;
  }
  .modal-header {
    display: flex;
    align-items: center;
    height: 4.5rem;
    padding-left: 2rem;
    border-bottom: 1px solid $G-color-border;
    font-weight: 700;
    &__icon-close {
      top: 50%;
      right: 1rem;
      width: 2.8rem;
      height: 2.8rem;
      border: 1px solid $G-color-border;
      border-radius: 100%;
      transform: translateY(-50%);
      &::after {
        background-size: 44%;
      }
      &:hover {
        background-color: $G-checked-background-color;
      }
    }
  }
  .modal-body {
    padding: 2rem;
    .c-table {
      tbody td:first-child {
        cursor: default;
      }
    }
  }
  .modal-footer {
    padding: 1rem 0 3rem;
  }
}

