/* SCSS Document */
@charset "UTF-8";

/****************************************************************************
  매체 분석 한정 스타일
****************************************************************************/
#container.trend {
  /*
  ■■■■■■■■■■■■■■■■■■■■■■■■■■■■■■■■■■■■■■■■■■■■■■■■■■■■■■■■■■■■■■■■■■■■■■■■■■■■■■■■■■■■■■■■■■■■■■■■■■
  */

  /* 전체 이슈 현황 */
  .l-section[data-section="전체이슈현황"] {
    .l-card[data-card="전체이슈현황"] {
      .l-card-header {
        color: #999999;
        font-size: 1.2rem;

        .count {
          color: #333333;
          padding: 0 0.3rem;
        }
      }

      .c-table {
        > thead {
          tr {
            th {
              &.is-color-cfcfcf {
                background: #cfcfcf;
              }
            }
          }
        }

        > tbody {
          tr {
            td {
              .is-color-negative {
                color: #ef151e;
              }

              // js spark line
              .js-chart {
                width: 21.6rem;
                height: 3.5rem;
                margin: 0 auto;
              }

              .issue_anal_box {
                display: flex;
                justify-content: space-between;
                align-items: center;
                width: 100%;

                > strong {
                  width: calc(100% - 7.5rem);
                }

                .analyze_btn {
                  width: 7rem; //140
                  height: 2.4rem; //48
                  background-image: url(#{$G-img-path}/view/trend/analyze_btn.png);
                  background-size: 100%;
                  visibility: hidden;
                  text-indent: -9999px;
                  opacity: 0;
                  transition: 0.35s;
                  // @include transition($time: 0.8, $delay: #{$i * 0.05 + 0.5});
                }
              }
              &.pd-2 {
                padding: 0.25rem;

                .c-badge {
                  margin: 0.25rem 0 0.25rem 0.25rem;
                }
              }
            }

            &:hover {
              td {
                .analyze_btn {
                  visibility: visible;
                  opacity: 1;
                }
              }
            }
          }
        }
      }
    }

    //기간별연관어변화
    .l-card[data-card="기간별연관어변화"] {
      $card: "l-card[data-card='기간별연관어변화']";
      $item: summary-list__item;

      .l-card-body {
        .row {
          > div {
            width: 25%;

            .range_box {
              position: sticky;
              top: 0;
              left: 0;
              height: 5.6rem;
              background: #fafafa;
              text-align: center;
              z-index: 10;

              > .week {
                display: block;
                color: #333333;
                margin-bottom: 0.9rem;
                line-height: 1;
                font-size: 2.1rem;
              }

              > .week_detail {
                font-size: 1.2rem;
                color: #666666;
              }
            }

            .c-table {
              > thead {
                > tr {
                  > th,
                  > td {
                    //border-right: 1px solid #ffffff;

                    &:last-child {
                      border-right: 1px solid #a9aaac;
                    }
                  }
                }

                &.is-sticky {
                  tr {
                    th {
                      position: sticky;
                      top: 5.599rem;
                      z-index: 10;
                    }
                  }
                }
              }
              > tbody {
                > tr {
                  > th,
                  > td {
                    .keyword {
                      position: relative;

                      .tooltip {
                        position: absolute;
                        bottom: 80%;
                        left: 1rem;
                        z-index: 1;
                        width: 35rem;
                        padding: 1rem;
                        border: 1px solid #666666;
                        font-size: $G-font-size;
                        font-weight: 700;
                        line-height: 1.6em;
                        word-break: keep-all;
                        background: #ffffff;
                        box-shadow: 0px 4px 4px rgba(0, 0, 0, 0.15);
                        opacity: 0;
                        transform: translateY(-10%);
                        pointer-events: none;
                        @include transition;
                        &--is-active {
                          opacity: 1;
                          pointer-events: all;
                          transform: translateY(0);
                        }
                      }
                    }
                    .percent {
                      &:after {
                        content: "%";
                        display: inline-block;
                        padding-left: 0.2rem;
                      }
                    }

                    &:last-child {
                      border-right: 1px solid #a9aaac;
                    }
                  }

                  &.is-hover {
                    > th,
                    > td {
                      border-color: #fff;
                      background-color: #e8e4e2;
                    }
                  }
                }
              }
            }

            &:nth-child(4) {
              width: calc(25% + 0.1rem);
            }
            &:last-child {
              width: calc(25% + 0.1rem);

              .c-table {
                > thead {
                  > tr {
                    > th,
                    > td {
                      &:last-child {
                        border-right: 0;
                      }
                    }
                  }
                }
                > tbody {
                  > tr {
                    > th,
                    > td {
                      &:last-child {
                        border-right: 0;
                      }
                    }
                  }
                }
              }
            }
          }
        }
      }
    }

    .l-card[data-card="정보량및감성추이"] {
      $card: "l-card[data-card='정보량및감성추이']";

      .js-chart {
        .amcharts-category-axis {
          rect {
            fill-opacity: 0;
          }
        }
      }
    }
  }

  //기간별연관어변화
  .l-card[data-card="연관어비교"] {
    $card: &;
    $item: keyword-list__item;

    .keyword-list {
      position: relative;
      display: block;
      width: calc(100% - 7.5rem);
      height: calc(100% - 9rem);
      margin: 4rem auto 0;
      background-image: url(#{$G-img-path}/view/trend/keyword_map.png);
      background-position: center center;
      background-size: 50%;
      opacity: 0;
      pointer-events: none;
      @include transition(0.55);

      &__item {
        position: absolute;
        height: 5rem;
        width: 20rem;
        color: #333333;
        margin-top: -2.5rem !important;
        font-size: 1.3rem;
        font-weight: 700;
        box-sizing: border-box;
        opacity: 0;
        pointer-events: all;
        @include transition();

        .box-rounded {
          display: inline-flex;
          justify-content: center;
          align-items: center;
          border-radius: 100px;
          height: 5rem;
          max-width: 100%;
          padding: 0 3rem;
          vertical-align: middle;
          .tit[data-tooltip-text]::after {
            white-space: nowrap;
          }
        }
      }

      &.is-show {
        background-size: 100%;
        opacity: 1;
        @include transition(0.25);

        &:before {
          left: 28.5%;
          @include transition(0.25);
        }
        &:after {
          right: 30%;
          @include transition(0.25);
        }
      }

      &:before,
      &:after {
        position: absolute;
        top: 50%;
        display: block;
        width: 3.4rem;
        margin-top: -2.1rem;
        text-align: center;
        font-size: 1.6rem;
        font-weight: bold;
        @include transition(0.55);
      }
      &:before {
        content: "비교 기간";
        left: 40%;
      }
      &:after {
        content: "검색 기간";
        margin-right: -2.44rem;
        right: 40%;
      }
    }

    /* prettier-ignore */
    @at-root #{$card} .is-show .#{$item} {      
      $top: (--1: 0, --2: 25%, --3: 50%, --4: 75%, --5: 100%);
      $left: (--left: 0, --center: calc(50% - 20rem / 2), --right: calc(100% - 20rem));
      $color: (--left: #333, --center: #fff, --right: #333 );
      $align: (--left: right, --center: center, --right: left );
      $bgColor: (--left: #DFBB8E, --center: #0B396A, --right: #83E0FE);
      $transition: (--del1: 0.15, --del2: 0.2, --del3: 0.25, --del4: 0.3, --del5: 0.35);
      
      $values:
        1     map-get($top, --1)   map-get($left, --left)    map-get($color, --left)     map-get($align, --left)    map-get($bgColor, --left)     auto   "0"  ,
        2     map-get($top, --2)   map-get($left, --left)    map-get($color, --left)     map-get($align, --left)    map-get($bgColor, --left)     auto   "0 0 0 -3rem",
        3     map-get($top, --3)   map-get($left, --left)    map-get($color, --left)     map-get($align, --left)    map-get($bgColor, --left)     auto   "0 0 0 -7rem" , 
        4     map-get($top, --4)   map-get($left, --left)    map-get($color, --left)     map-get($align, --left)    map-get($bgColor, --left)     auto   "0 0 0 -3rem" ,
        5     map-get($top, --5)   map-get($left, --left)    map-get($color, --left)     map-get($align, --left)    map-get($bgColor, --left)     auto   "0",
        6     map-get($top, --1)   map-get($left, --center)  map-get($color, --center)   map-get($align, --center)  map-get($bgColor, --center)   100%   "0",
        7     map-get($top, --2)   map-get($left, --center)  map-get($color, --center)   map-get($align, --center)  map-get($bgColor, --center)   100%   "0" ,
        8     map-get($top, --3)   map-get($left, --center)  map-get($color, --center)   map-get($align, --center)  map-get($bgColor, --center)   100%   "0"  ,
        9     map-get($top, --4)   map-get($left, --center)  map-get($color, --center)   map-get($align, --center)  map-get($bgColor, --center)   100%   "0"  ,
        10    map-get($top, --5)   map-get($left, --center)  map-get($color, --center)   map-get($align, --center)  map-get($bgColor, --center)   100%   "0"  ,
        11    map-get($top, --1)   map-get($left, --right)   map-get($color, --right)    map-get($align, --right)   map-get($bgColor, --right)    auto   "0"  ,
        12    map-get($top, --2)   map-get($left, --right)   map-get($color, --right)    map-get($align, --right)   map-get($bgColor, --right)    auto   "0 0 0 5rem" ,
        13    map-get($top, --3)   map-get($left, --right)   map-get($color, --right)    map-get($align, --right)   map-get($bgColor, --right)    auto   "0 0 0 9rem"  , 
        14    map-get($top, --4)   map-get($left, --right)   map-get($color, --right)    map-get($align, --right)   map-get($bgColor, --right)    auto   "0 0 0 5rem"  ,
        15    map-get($top, --5)   map-get($left, --right)   map-get($color, --right)    map-get($align, --right)   map-get($bgColor, --right)    auto   "0"  ,
      ;

      @each $index, $top, $left, $color, $align, $bgColor, $width, $margin, $transition in $values {
        &:nth-child(#{$index}) {
          top: #{$top};
          left: #{$left};
          margin: #{$margin};
          color: #{$color};
          text-align: #{$align};
          opacity: 1;
          
          .box-rounded {
            width: #{$width};
            background-color: #{$bgColor};
          }
        }
      }
    }
  }

  /*
  ■■■■■■■■■■■■■■■■■■■■■■■■■■■■■■■■■■■■■■■■■■■■■■■■■■■■■■■■■■■■■■■■■■■■■■■■■■■■■■■■■■■■■■■■■■■■■■■■■■
  */

  /* 전체데이터 */
  .l-card[data-card="전체데이터"],
  .l-card[data-card="데이터목록"] {
    .l-card-header {
      color: #999999;
      font-size: 1.2rem;

      > h4 {
        color: #333333;
      }

      .count {
        color: #333333;
        padding: 0 0.3rem;
      }
    }
  }

  /* 공통 */

  //페이지 공통 차트 스타일 (height 가변성 고려)
  .js-chart {
    .amcharts-main-div {
      .amcharts-chart-div {
        height: 100% !important;
      }

      .amcharts-pie-item {
        cursor: pointer;
      }
    }
  }

  // 클릭영역 스타일 - 태그 색상
  .category-tag {
    color: #ffffff !important;
  }

  // 퍼센트 증감률
  [class^="icon"]:not([class*="circle"]) {
    margin-right: 0.2rem;
    padding-top: 0.1rem;
    font-size: 0.7em;
    &[class*="up"] {
      --icon-svg-color: #ff0000;
    }
    &[class*="bar"] {
      --icon-svg-color: #acacac;
    }
    &[class*="down"] {
      --icon-svg-color: #0000ff;
    }
  }

  /*
  ■■■■■■■■■■■■■■■■■■■■■■■■■■■■■■■■■■■■■■■■■■■■■■■■■■■■■■■■■■■■■■■■■■■■■■■■■■■■■■■■■■■■■■■■■■■■■■■■■■
  */
}
