/* SCSS Document */
@charset "UTF-8";

/****************************************************************************
  매체 분석 한정 스타일
****************************************************************************/
#container.media {
  /*
  ■■■■■■■■■■■■■■■■■■■■■■■■■■■■■■■■■■■■■■■■■■■■■■■■■■■■■■■■■■■■■■■■■■■■■■■■■■■■■■■■■■■■■■■■■■■■■■■■■■
  */

  /* 전체 토픽 현황 */
  .l-section[data-section="전체토픽현황"] {
    //주요현황요약
    .l-card[data-card="주요현황요약"] {
      $card: "l-card[data-card='주요현황요약']";
      $item: summary-list__item;

      .summary-list {
        display: flex;
        justify-content: space-between;
        height: 100%;
        margin: 0 -0.5rem;
        &__item {
          display: flex;
          flex: 1;
          flex-direction: column;
          position: relative;
          padding: 0 0.5rem;
          &:after {
            content: "";
            position: absolute;
            top: 0;
            bottom: 0;
            left: 0.5rem;
            right: 0.5rem;
            z-index: 1;
            border: 1px solid $G-color-border;
            pointer-events: none;
          }
          .graph-box {
            $colorSet: --color-set;

            overflow: hidden;
            position: relative;
            height: 23rem;
            background-color: var($colorSet, #424242);
            .tit {
              position: absolute;
              top: 1.5rem;
              left: 50%;
              font-size: 1.5rem;
              font-weight: 700;
              color: #fff;
              text-align: center;
              white-space: nowrap;
              transform: translateX(-50%);
            }
            .graph {
              position: absolute;
              left: 50%;
              bottom: -1px;
              width: 5rem;
              transform: translateX(-50%) scaleY(0.6);
              transform-origin: bottom;
              .percent {
                position: absolute;
                left: 50%;
                top: 100%;
                font-size: 2.1rem;
                font-weight: bold;
                color: #fff;
                white-space: nowrap;
                transform: translate(-50%, -160%) scaleY(1.6);
                &::after {
                  content: "%";
                  font-weight: normal;
                }
              }
              &::after {
                content: "";
                position: absolute;
                left: 0;
                bottom: 0;
                width: 100%;
                height: 100%;
                background-color: rgba(#fff, 0.5);
                transform: translateY(100%);
              }
            }

            @at-root .#{$card}.l-card--is-active .#{$item} {
              @for $i from 1 through 10 {
                &:nth-child(#{$i}) .graph {
                  &::after {
                    transform: translateY(0) !important;
                    @include transition($time: 0.8, $delay: #{$i * 0.05 + 0.5});
                  }
                  .percent {
                    top: 0 !important;
                    @include transition($time: 0.8, $delay: #{$i * 0.05 + 0.5});
                  }
                }
              }
            }
          }
          .info-box {
            padding: 1.5rem 0;
            text-align: center;
            .tit {
              font-size: 1.5rem;
              font-weight: 700;
              color: #666;
              text-align: center;
            }
            .count {
              display: inline-block;
              position: relative;
              font-size: 2.1rem;
              font-weight: 700;
              text-align: center;
              cursor: pointer;
              &::after {
                content: "";
                position: absolute;
                left: 0;
                bottom: 0;
                width: 0;
                border-bottom: 2px solid $G-color-default;
                @include transition;
              }
              &:hover::after {
                width: 100%;
              }
            }
            .rate {
              display: flex;
              justify-content: center;
              align-items: center;
              font-size: 1.3rem;
              letter-spacing: 0;
              &::before {
                content: "(";
                display: inline-block;
                padding-right: 0.2rem;
              }
              &:after {
                content: "%)";
                display: inline-block;
                padding-left: 0.2rem;
              }
              [class^="icon"] {
                margin-right: 0.2rem;
                padding-top: 0.1rem;
                font-size: 0.8em;
                &[class*="up"] {
                  --icon-svg-color: #ff0000;
                }
                &[class*="bar"] {
                  --icon-svg-color: #acacac;
                }
                &[class*="down"] {
                  --icon-svg-color: #0000ff;
                }
              }
            }
          }
          .emotion-list {
            flex: 1;
            background-color: #f4f4f4;
            .tit {
              display: flex;
              justify-content: center;
              align-items: center;
              height: 2.3rem;
              margin-bottom: 1.7rem;
              font-size: 1.2rem;
              font-weight: 700;
              background-color: #ededef;
            }
            .desc {
              display: flex;
              justify-content: center;
              align-items: center;
              margin-bottom: 1rem;
              [class^="icon"] {
                &[class*="positive"] {
                  --icon-svg-color: #5ba1e0;
                }
                &[class*="neutral"] {
                  --icon-svg-color: #808080;
                }
                &[class*="negative"] {
                  --icon-svg-color: #ea7070;
                }
              }
              .percent {
                display: inline-block;
                width: 4.5rem;
                font-size: 1.2rem;
                text-align: right;
                line-height: 1em;
                &::after {
                  content: "%";
                  font-weight: normal;
                }
              }
            }
          }
        }
      }
    }
    // 주요이슈
    .l-card[data-card="주요이슈"] .l-card-body {
      z-index: 1;
    }
    .ol_list {
      $card: "l-card[data-card='주요이슈']";
      $list: ol_list;
      height: 42rem;
      counter-reset: number 0;
      > li {
        opacity: 0;
        transform: translateY(100%);
        .ol_list_box {
          display: table;
          height: 3.2rem;
          margin-bottom: 1rem;
          background: #f4f4f4;
          line-height: 3.2rem;
          text-align: center;
          width: 100%;

          > * {
            display: table-cell;
            padding: 0 0.5rem;          
            &:last-child {
              width: 8.2rem;
              padding-right: 1rem;
              text-align: right;
            }
          }

          > .rank {
            width: 3.2rem;
            padding: 0;
            font-weight: 700;
            background: #888888;
            color: #ffffff;
            &::before {
              counter-increment: number 1;
              content: counter(number);
            }
          }

          > .tit {
            position: relative;
            padding-left: 1rem;
            text-align: left;
            font-weight: bold;
          }

          > .cnt {
            width: 6rem;
            font-weight: 700;
            text-align: right;            
          }

           .percent {
            width: 8.2rem;
            font-size: 1.2rem;    
            &::after {
              content: '%';
            }
          }
          [class^="icon"] {
            margin-right: 0.3rem;
            padding-top: 0.1rem;
            font-size: 0.7rem;
            &[class*="up"] {
              --icon-svg-color: #ff0000;
            }
            &[class*="bar"] {
              --icon-svg-color: #acacac;
            }
            &[class*="down"] {
              --icon-svg-color: #0000ff;
            }
          }
        }

        &:last-child {
          .ol_list_box {
            margin-bottom: 0;
          }
        }

        @at-root .#{$card}.l-card--is-active .#{$list} li {
          @for $i from 1 through 10 {
            &:nth-child(#{$i}) {
              opacity: 1 !important;
              transform: translateY(0) !important;
              @include transition($time: 0.8, $delay: #{$i * 0.05 + 0.5});
            }
          }
        }
      }
    }
  }

  /*
  ■■■■■■■■■■■■■■■■■■■■■■■■■■■■■■■■■■■■■■■■■■■■■■■■■■■■■■■■■■■■■■■■■■■■■■■■■■■■■■■■■■■■■■■■■■■■■■■■■■
  */

  /* 토픽상세현황 */
  .l-section[data-section="토픽상세현황"] {
    .l-card[data-card="연관키워드정보량"] {
      .cnt_box {
        margin: 0 -2rem;

        > .cnt_box_row {
          display: table;
          width: 100%;

          > .cnt_box_cell {
            display: table-cell;
            position: relative;
            width: 50%;
            padding: 1.1rem 2rem;
            border-bottom: 1px solid #ededef;
            box-sizing: border-box;

            &:after {
              content: "";
              position: absolute;
              top: 50%;
              right: 0;
              width: 1px;
              height: 3rem;
              background: #ededef;
              margin-top: -1.5rem;
            }

            .cnt_box_wrap {
              display: inline-block;
              width: 17.4rem;
              height: 3rem;
              line-height: 3rem;

              > * {
                display: inline-block;
                width: 50%;
              }

              > .cnt_box_label {
                text-align: left;
                color: #666666;
                font-size: 1.2rem;
              }
              > .cnt_box_cnt {
                text-align: right;
                color: #333333;
                font-size: 1.7rem;
                font-weight: 700;

                &.new {
                  color: #eb2020;
                }
              }
            }
            &:first-child {
              text-align: right;
            }
            &:last-child {
              border-right: 0;
              text-align: left;

              &:after {
                content: none;
              }
            }
          }
          &:last-child {
            > .cnt_box_cell {
              border-bottom: 0;
            }
          }
        }
      }
    }
  }

  /*
  ■■■■■■■■■■■■■■■■■■■■■■■■■■■■■■■■■■■■■■■■■■■■■■■■■■■■■■■■■■■■■■■■■■■■■■■■■■■■■■■■■■■■■■■■■■■■■■■■■■
  */

  /* 전체데이터 */
  .l-section[data-section="전체데이터"] {
    .l-card[data-card="전체데이터"] {
      .l-card-header {
        color: #999999;
        font-size: 1.2rem;

        .count {
          color: #333333;
          padding: 0 0.3rem;
        }
      }
    }
  }

  /*
  ■■■■■■■■■■■■■■■■■■■■■■■■■■■■■■■■■■■■■■■■■■■■■■■■■■■■■■■■■■■■■■■■■■■■■■■■■■■■■■■■■■■■■■■■■■■■■■■■■■
  */

  /* 공통 */

  //페이지 공통 차트 스타일 (height 가변성 고려)
  .js-chart {
    .amcharts-main-div {
      .amcharts-chart-div {
        height: 100% !important;
      }

      .amcharts-pie-item {
        cursor: pointer;
      }
    }
  }

  // 클릭영역 스타일 - 태그 색상
  .category-tag {
    color: #ffffff !important;
  }

  /*
  ■■■■■■■■■■■■■■■■■■■■■■■■■■■■■■■■■■■■■■■■■■■■■■■■■■■■■■■■■■■■■■■■■■■■■■■■■■■■■■■■■■■■■■■■■■■■■■■■■■
  */
}
